import { NgModule } from "@angular/core";
import { PreloadAllModules, RouterModule, Routes } from "@angular/router";
import { AuthGuard } from "./services/auth-guard.service";

const routes: Routes = [
  { path: "", redirectTo: "splash", pathMatch: "full" },
  {
    path: "splash",
    loadChildren: "./pages/splash/splash.module#SplashPageModule"
  },
  {
    path: "tabs",
    loadChildren: () =>
      import("./tabs/tabs.module").then((m) => m.TabsPageModule),
    canActivate: [AuthGuard]
  },
  // {
  //   path: "game",
  //   loadChildren: () =>
  //     import("./pages/game/game.module").then((m) => m.GamePageModule),
  //   canActivate: [AuthGuard],
  // },
  // {
  //   path: "admin",
  //   loadChildren: () =>
  //     import("./pages/admin/admin.module").then((m) => m.AdminPageModule),
  //   canActivate: [AuthGuard],
  // },
  // {
  //   path: "factions",
  //   loadChildren: "./pages/faction/faction.module#FactionPageModule",
  // },
  { path: "**", loadChildren: "./pages/splash/splash.module#SplashPageModule" }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
