import { Component, Input, Output, EventEmitter } from "@angular/core";
import { Abilities, AbilityTranslator } from "../../../assets/Constants";

@Component({
  selector: "brain",
  templateUrl: "brain.html"
})
export class BrainComponent {
  @Input() payload;

  @Input() mine = false;
  @Input() showBar = true;
  @Input() target = "all";
  @Input() details = false;
  @Input() brainScore = 0;
  @Input() stat = "";
  @Output() onChangeCategory = new EventEmitter();

  brain = null;
  initial = {
    memory: { color: "#ff0000", value: 0 },
    agility: { color: "#ff0000", value: 0 },
    math: { color: "#ff0000", value: 0 },
    accuracy: { color: "#ff0000", value: 0 },
    focus: { color: "#ff0000", value: 0 },
    imagination: { color: "#ff0000", value: 0 },
    brainScore: { color: "#ff0000", value: 0 }
  };

  brainScoreCount = 0;

  abilities = Abilities;
  abilityTranslator = AbilityTranslator;

  indicatorStyle: any;
  interval: any;

  constructor() {}

  ngOnChanges() {
    console.log("chagnged");

    setTimeout(() => {
      this.brain = this.payload;
      // this.brainScoreCounter(this.brainScore * 10)
    }, 50);
  }

  selectCategory(category: string) {
    if (this.target == category) return;
    this.onChangeCategory.emit(category);
  }

  brainScoreCounter(score) {
    if (score) {
      clearInterval(this.interval);
      this.interval = setInterval(() => {
        this.brainScoreCount < score
          ? this.brainScoreCount++
          : clearInterval(this.interval);
      }, 200 / score);
    }
  }
}
