import { Injectable } from "@angular/core";
import { Storage } from "@ionic/storage";
import { SmartAudioProvider } from "./smart-audio";
import { BehaviorSubject } from "rxjs";
@Injectable({
  providedIn: "root"
})
export class DataProvider {
  // Release Check
  APP_VERSION = "1.1.42";
  client = "WEB"; // WEB, GOOGLE, APPLE
  // Release Check

  platform = "WEB"; // APP, WEB

  developerMode: boolean = true;
  adminMode: boolean = false;

  // baseURL = "https://haraka-api.anyaco.net";
  baseURL = "http://127.0.0.1:3000";

  auth: string;

  CLIENTS = {
    GOOGLE: "GOOGLE",
    APPLE: "APPLE",
    WEB: "WEB"
  };

  PLATFORMS = {
    APP: "APP",
    WEB: "WEB"
  };

  player = {
    role: "PLAYER",
    name: "",
    brain: {
      brainScore: {
        value: 0
      }
    },
    level: 0,
    gold: 0,
    silver: 0,
    gem: 0,
    xp: 0,
    id: 0,
    code: "HOOSH",
    trophy: 0,
    games: 0,
    rank: 0,
    wins: 0,
    realtime: false,
    avatar: null,
    mobile: null,
    email: null,
    province: null,
    invited: 0,
    clanId: null,
    clanRole: null,
    eventReward: null,
    tickets: 0,
    salawat: 0
  };

  countries: any;
  baseCountries = [];

  live: any = {
    id: null,
    sessionId: null,
    willStartAt: null,
    lastLive: null,
    prize: "",
    winnersCount: 0,
    questions: [],
    questionsLength: 0,
    leaderboard: []
  };

  games = []; // list of all games
  game: any;
  startGame = { ready: false }; // state of home page. ready is true when start game button clicked

  currentIsland = { index: null };

  quests = { retention: null, trophy: null, brain: null, daily: null };

  badges = {
    quest: false,
    map: false,
    inbox: false,
    clan: false
  };

  // app config like constance parameter
  config = { limits: null, scoring: null, scales: null };

  // observable
  needToSetIsland = new BehaviorSubject(false);

  // show is true when a popup on screen, use to blur background page
  isShowingAlert = new BehaviorSubject(false);

  constructor(
    private storage: Storage,
    private smartAudioProvider: SmartAudioProvider
  ) {}

  setIslandSignal() {
    this.needToSetIsland.next(true);
  }

  setPlayer(payload: any) {
    Object.keys(payload).map((key) => {
      // this.playSound(key, this.player[key], payload[key]);
      this.player[key] = payload[key];
    });
  }

  async setAuth(token: any) {
    this.auth = token;

    await this.setTostorage("token", token);
  }

  setCountries(countries: any) {
    countries.sort((a, b) => a.id - b.id);
    countries.map((country) => {
      country.cities.sort((a, b) => a.id - b.id);
      this.baseCountries.push({ cities: [...country.cities] });
    });
    this.countries = countries;
    this.setGames(countries);
  }

  setGames(countries: any) {
    let temp = [];
    countries.map((country) => {
      country.cities.sort((a, b) => a.id - b.id);
      temp = [...temp, ...country.cities];
    });
    this.games.splice(0, this.games.length, ...temp);
  }

  setGame(payload: any) {
    this.game = payload;
  }

  setStartGame(payload: any) {
    this.startGame.ready = payload;
  }

  setIslandIndex(index: number) {
    this.currentIsland.index = index;
  }

  setBadges(payload: any) {
    payload.quest !== undefined ? (this.badges.quest = payload.quest) : "";
    payload.map !== undefined ? (this.badges.map = payload.map) : "";
    payload.inbox !== undefined ? (this.badges.inbox = payload.inbox) : "";
    payload.clan !== undefined ? (this.badges.clan = payload.clan) : "";

    let sound = false;
    Object.values(payload).map((badge) => {
      if (badge) sound = true;
    });

    if (sound) {
      this.smartAudioProvider.play("notification");
    }
  }

  setQuests(payload: any) {
    payload.retentionQuest
      ? (this.quests.retention = payload.retentionQuest)
      : "";
    payload.trophyQuest ? (this.quests.trophy = payload.trophyQuest) : "";
    payload.brainQuest ? (this.quests.brain = payload.brainQuest) : "";
    payload.dailyQuests ? (this.quests.daily = payload.dailyQuests) : "";

    if (payload.retentionQuest) {
      if (payload.retentionQuest.active) {
        this.setBadges({ quest: true });
      }
    }
  }

  setAlertPage(payload: any) {
    this.isShowingAlert.next(payload);
  }

  setConfig(payload: any) {
    payload.limits ? (this.config.limits = payload.limits) : "";
    payload.scoring ? (this.config.scoring = payload.scoring) : "";
    payload.scales ? (this.config.scales = payload.scales) : "";
  }

  getPlayer() {
    return this.player;
  }

  getCountries() {
    return this.countries;
  }

  getBaseCountries() {
    return this.baseCountries;
  }

  getGames() {
    return this.games;
  }

  getGame() {
    return this.game;
  }

  getIslandIndex() {
    return this.currentIsland;
  }

  getBadges() {
    return this.badges;
  }

  getQuests() {
    return this.quests;
  }

  getConfig() {
    return this.config;
  }

  getAuth() {
    if (!this.auth) {
      return this.getFromStorage("token");
    } else {
      return this.auth;
    }
  }

  playSound(key: string, old: number, current: number) {
    switch (key) {
      case "silver":
        if (old < current) this.smartAudioProvider.play("coin");
        break;
      case "trophy":
        if (old < current) this.smartAudioProvider.play("trophyDecrease");
        break;
    }
  }

  async setTostorage(key: string, value: any) {
    await this.storage.set(key, value);
  }

  async getFromStorage(key: string) {
    let value = null;
    await this.storage.get(key).then((val) => {
      value = val;
    });

    return value;
  }
}
