import { Component } from "@angular/core";
import { ModalController } from "@ionic/angular";
import { SmartAudioProvider } from "../../services/smart-audio";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "./../../services/data";

@Component({
  selector: "page-others",
  templateUrl: "others.html"
})
export class OthersPage {
  sfx = {
    title: "Sound Effects",
    image: "sfx",
    value: true
  };

  music = {
    title: "Background Music",
    image: "music",
    value: true
  };

  needToUpdatePreferences = null;

  isShowingAlert = false;

  constructor(
    public modalCtrl: ModalController,
    public httpProvider: HttpProvider,
    public dataProvider: DataProvider,
    public smartAudioProvider: SmartAudioProvider
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });
    this.sfx.value = !this.smartAudioProvider.getmutedSFX();
    this.music.value = !this.smartAudioProvider.getmutedMusic();
    setTimeout(() => {
      this.needToUpdatePreferences = false;
    }, 0);
  }

  ionViewWillLeave() {
    if (this.needToUpdatePreferences) {
      this.updateSettingChanges();
    }
  }

  toggleSFXMuteness() {
    if (this.needToUpdatePreferences === null) {
      return;
    }

    this.smartAudioProvider.play("change");
    this.smartAudioProvider.toggleSFXMuteness();
    this.needToUpdatePreferences = true;
  }

  toggleMusicMuteness() {
    if (this.needToUpdatePreferences === null) {
      return;
    }

    this.smartAudioProvider.play("change");
    this.smartAudioProvider.toggleMusicMuteness();
    this.needToUpdatePreferences = true;
  }

  updateSettingChanges() {
    const body = {
      music: this.music.value,
      sfx: this.sfx.value
    };
    this.httpProvider.post(
      "/users/preferences",
      body,
      () => {},
      () => {}
    );
  }

  contactSupport() {
    if (this.dataProvider.platform == "APP") {
      // this.socialSharing.shareViaEmail(
      //   "Type your message..",
      //   "Contact Support",
      //   ["harakaquiz@gmail.com"], // TO: must be null or an array
      //   null, // CC: must be null or an array
      //   null, // BCC: must be null or an array
      //   null // FILES: can be null, a string, or an array
      //   // () => {}, // called when sharing worked, but also when the user cancelled sharing via email. On iOS, the callbacks' boolean result parameter is true when sharing worked, false if cancelled. On Android, this parameter is always true so it can't be used). See section "Notes about the successCallback" below.
      //   // () => {} // called when sh*t hits the fan
      // );
    } else {
      // try {
      //   navigator
      //     .share({
      //       title: "Optional title",
      //       text: "Optional message",
      //       url: "http://www.myurl.com"
      //     })
      //     .then(function () {
      //       console.log("Successful share");
      //     })
      //     .catch(function (error) {
      //       console.log("Error sharing:", error);
      //     });
      // } catch (error) {}
    }
  }

  dismiss() {
    this.smartAudioProvider.play("back");
    this.modalCtrl.dismiss();
  }
}
