@Injectable({
  providedIn: "root"
})
export class UtilsProvider {
  playerRoles = PlayerRoles;

  constructor(
    private smartAudioProvider: SmartAudioProvider,
    private dataProvider: DataProvider,
    private httpProvider: HttpProvider,
    private clanService: ClanService,
    private navCtrl: NavController,
    private toastCtrl: ToastController,
    private router: Router
  ) {}

  updateUser(fields: any, response?: Function) {
    const body = { fields };

    this.httpProvider.post("/users/data", body, (res: any) => {
      this.dataProvider.setPlayer(res.data);
      response(res.data);
    });
  }

  validateForm(formData: any, validateItems: any) {
    let errorText = "<ul>";

    validateItems.map((item: any) => {
      let hasError = 0;

      if (item.type == "MIN")
        if (
          typeof formData[item.field] != "number" &&
          formData[item.field].length < item.value
        )
          hasError++;
        else if (
          typeof formData[item.field] == "number" &&
          formData[item.field] < item.value
        )
          hasError++;

      if (item.type == "MAX")
        if (
          typeof formData[item.field] != "number" &&
          formData[item.field].length > item.value
        )
          hasError++;
        else if (
          typeof formData[item.field] == "number" &&
          formData[item.field] > item.value
        )
          hasError++;

      if (item.type == "boolean")
        if (!!formData[item.field] != item.value) hasError++;

      if (item.type == "required") if (!formData[item.field]) hasError++;

      if (hasError) errorText += `<li>${item.message}</li>`;
    });

    errorText += "</ul>";

    if (errorText != "<ul></ul>") {
      const payload = {
        code: "CUSTOM_HTML_ALERT",
        text: errorText,
        dismissButton: "Got it"
      };
      return { error: true, payload };
    }

    return { error: false };
  }

  setTime(date: any) {
    let diffTime = moment(date).fromNow(true);

    return String(diffTime);
  }

  fixNumbers(str) {
    let arabicNumbers = ["٠", "١", "٢", "٣", "٤", "٥", "٦", "٧", "٨", "٩"];
    let persianNumbers = ["۰", "۱", "۲", "۳", "۴", "۵", "۶", "۷", "۸", "۹"];
    let iphoneNumbers = ["۰", "۱", "۲", "۳", "۴", "۵", "۶", "۷", "۸", "۹"];

    if (typeof str === "string" && str.length > 0) {
      str.split("").map(() => {
        for (var i = 0; i < 10; i++) {
          str = str
            .replace(persianNumbers[i], i)
            .replace(arabicNumbers[i], i)
            .replace(iphoneNumbers[i], i);
        }
      });
    }

    return str;
  }

  checkMobileFormat(mobile: string) {
    return /^[0][9][0-9]{9}$/.test(mobile);
  }

  checkInput(event: any, input: string) {
    let value = null;
    let callback = (res: any, err: any): any => {
      err();
    };

    if (event) {
      if (event.detail) {
        if (event.detail.value) {
          if (event.detail.value.length > 2) {
            value = event.detail.value;
          }
        }
      }
    }

    if (!value) return callback;

    let body = {};

    if (input == "username") {
      body = { name: value };
    }

    if (input == "email") {
      if (!/\S+@\S+\.\S+/.test(value)) {
        return callback;
      }
      body = { email: value };
    }

    callback = (res, err) => {
      this.httpProvider.post("/users/auth/checkInput", body, res, err);
    };

    return callback;
  }

  setDataAndGoToGame(data: any) {
    this.dataProvider.setAuth(data.token);
    this.dataProvider.setPlayer({ ...data.user, brain: data.brain });
    this.clanService.setClan(data.clan);
    this.dataProvider.setCountries(data.countries);
    this.dataProvider.setBadges(data.badges);
    this.dataProvider.setQuests(data.quests);
    this.dataProvider.setConfig(data.config);
    this.smartAudioProvider.setMusic(data.user.preferences);

    setTimeout(() => {
      this.navCtrl.setDirection("root");
      this.router.navigate(["tabs"]);
    }, 909);
  }

  async presentToast(
    message: any,
    buttons: any = null,
    position: any = "middle",
    duration: number = 3000,
    mode: any = "ios",
    callback?: Function
  ) {
    const toast = await this.toastCtrl.create({
      message,
      buttons,
      position,
      duration,
      mode,
      cssClass: "shahoosh-toast"
    });
    toast.present();

    if (typeof callback == "function" && callback) {
      toast.onDidDismiss().then((data) => {
        callback(data.role);
      });
    }
  }

  openKeyboard(event: any, typing: boolean) {
    if (typing) {
      if (event) {
        return { transform: `translateY(-${event.keyboardHeight}px)` };
      } else if (this.dataProvider.platform == "WEB") {
        return { transform: `translateY(-${250}px)` };
      }
    }
  }

  handleDirection(event: any, message: string) {
    if (event.key != " ") {
      if (
        /^[آ ا ب پ ت ث ج چ ح خ د ذ ر ز ژ س ش ص ض ط ظ ع غ ف ق ک گ ل م ن و ه ی ي ۱ ۲ ۳ ۴ ۵ ۶ ۷ ۸ ۹ ۰ 1 2 3 4 5 6 7 8 9 0]/.test(
          message[0]
        ) ||
        !message.length
      ) {
        return { direction: "rtl" };
      } else {
        return { direction: "ltr", "text-align": "left" };
      }
    }
  }
}
