import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { DataProvider } from "./data";
import { timeout } from "rxjs/operators";
import { BehaviorSubject } from "rxjs";

let baseURL = "https://haraka-api.anyaco.net";

@Injectable({
  providedIn: "root"
})
export class HttpProvider {
  needToShowError = new BehaviorSubject(false);
  needToRegister = new BehaviorSubject(false);

  constructor(public http: HttpClient, private dataProvider: DataProvider) {
    baseURL = this.dataProvider.baseURL;
  }

  async get(method, completion, errorHandler?, maximumTimeout = 10000) {
    let headers = new HttpHeaders();
    const token = await this.dataProvider.getAuth();
    if (token) {
      headers = headers.append("x-auth", token);
    }

    this.http
      .get(baseURL + method, { headers })
      .pipe(timeout(maximumTimeout))
      .subscribe(
        (data) => completion(data),
        (error) =>
          errorHandler
            ? errorHandler(error.status, method, completion)
            : this.error(error.status, error.error, method, null, completion)
      );
  }

  async post(method, body, completion, errorHandler?, maximumTimeout = 10000) {
    let headers = new HttpHeaders();
    const token = await this.dataProvider.getAuth();
    if (token) {
      headers = headers.append("x-auth", token);
    }

    this.http
      .post(baseURL + method, body, { headers })
      .pipe(timeout(maximumTimeout))
      .subscribe(
        (data) => completion(data),
        (error) => {
          if (
            errorHandler &&
            error.status != 401 &&
            error.status != 406 &&
            error.status != 409 &&
            error.status != 500 &&
            error.status != 502
          ) {
            errorHandler(error);
          } else {
            this.error(error.status, error.error, method, body, completion);
          }
        }
      );
  }

  error(errorCode, error, methode, body, completion) {
    switch (errorCode) {
      case 401:
        this.needRegister(error.data.avatars);
        break;
      case 409:
        this.presentAlert({ code: "ALREADY_REQUEST_PARTICIPATE" });
        break;
      case 500:
      case 502:
        this.presentAlert({ code: "SERVER_ERROR" });
        break;
      default:
        let retry = () => {
          body
            ? this.post(methode, body, completion)
            : this.get(methode, completion);
        };
        this.presentAlert({ code: "CONNECTION_ERROR", retry });
        break;
    }
  }

  presentAlert(payload) {
    this.needToShowError.next(payload);
  }

  needRegister(avatars) {
    this.needToRegister.next(avatars);
  }
}
