import { Pipe, PipeTransform } from "@angular/core";
import * as moment from "moment";

@Pipe({
  name: "liveTimer"
})
export class LiveTimerPipe implements PipeTransform {
  aDay = 86400000;

  transform(value: any, ...args) {
    if (value > this.aDay) {
      return moment
        .utc(moment.duration(value).asMilliseconds())
        .format("D:HH:mm:ss");
    } else {
      return moment
        .utc(moment.duration(value).asMilliseconds())
        .format("H:mm:ss");
    }
  }
}
