import { Component, Input } from '@angular/core';


@Component({
  selector: 'points-bar',
  templateUrl: 'points-bar.html'
})
export class PointsBarComponent {

  @Input() playerPoint: number
  @Input() opponentPoint: number
  @Input() result = false
  @Input() animated = false

  style: any

  constructor() {
  }

  ngOnChanges() {    
    const pp = Math.max(0, this.playerPoint)
    const op = Math.max(0, this.opponentPoint)
    let translate = pp + op == 0 ? 50 : pp * 100 / (pp + op)
    this.style = { 'transform': 'translateX(' + translate + '%)' }
  }
}
