import { Injectable } from "@angular/core";
import { ModalController, NavController } from "@ionic/angular";
import { Router } from "@angular/router";

import { DataProvider } from "./data";

import { AlertsPage } from "../pages/alerts/alerts";
import { ProfilePage } from "../pages/profile/profile";
import { OthersPage } from "../pages/others/others";

@Injectable({
  providedIn: "root"
})
export class NavigateService {
  constructor(
    private navCtrl: NavController,
    private modalCtrl: ModalController,
    private router: Router,
    private dataProvider: DataProvider
  ) {}

  async profile(componentProps = null) {
    const profileModal = await this.modalCtrl.create({
      component: ProfilePage,
      componentProps
    });
    profileModal.present();
  }

  async clanProfile(componentProps = null) {
    const clanProfileModal = await this.modalCtrl.create({
      component: ProfilePage,
      componentProps: { isForMe: false, ...componentProps, clanProfile: true }
    });
    clanProfileModal.present();
  }

  async shop(state = null) {}

  async others(componentProps = null) {
    const othersModal = await this.modalCtrl.create({
      component: OthersPage,
      componentProps
    });
    othersModal.present();
  }

  async presentAlert(payload: any, callback?: Function) {
    if (!this.dataProvider.isShowingAlert.value) {
      let alertModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      alertModal.present();
      if (typeof callback == "function") {
        alertModal.onDidDismiss().then(async (res) => callback(res.data));
      }
    }
  }
}
