import { Pipe, PipeTransform } from "@angular/core";
import * as moment from "moment";

@Pipe({
  name: "duration"
})
export class DurationPipe implements PipeTransform {
  transform(value: number, ...args) {
    if (value > 86400000) {
      return (
        moment
          .utc(moment.duration(value - 86400000 + 1).asMilliseconds())
          .format("D") + " Days"
      );
    } else if (value > 3600000) {
      return moment
        .utc(moment.duration(value).asMilliseconds())
        .format("HH:mm:ss");
    } else if (value > 60000) {
      return moment
        .utc(moment.duration(value).asMilliseconds())
        .format("mm:ss");
    } else {
      return moment.utc(moment.duration(value).asMilliseconds()).format("ss");
    }
  }
}
