import { Injectable } from "@angular/core";
import { Howl } from "howler";
@Injectable({
  providedIn: "root"
})
export class SmartAudioProvider {
  sounds: any = [];
  current: any;
  mutedMusic = { mutedMusic: true, mutedSFX: true };

  assetsPrefix = "assets/audio/";
  audios = [
    // loop audios for home page
    "HomePlay",

    // general sounds
    "salawat",
    "buttonClick",
    "submit",
    "back",
    "prize",
    "notification",
    "chest",
    "achive",
    "slide",
    "noMoney",
    "coin",
    "trophy",
    "trophyDecrease",
    "levelUp",
    "change",
    "energy",
    "islandUnlock",

    // in game sounds
    "finding",
    "wrong",
    "correct",
    "win",
    "lose",
    "start",
    "started",
    "ended",
    "enterQuestion",
    "find"
  ];

  constructor() {
    this.preloadAudios();
  }

  ngOnDestroy() {
    this.stop("HomePlay");
    this.stop("GamePlay");
  }

  preloadAudios() {
    this.audios.forEach((audioKey) => {
      this.preload(audioKey, [
        this.assetsPrefix + audioKey + ".aac",
        this.assetsPrefix + audioKey + ".ogg"
      ]);
    });
  }

  preload(key: string, src: any) {
    const audio = new Howl({ src: [...src], loop: key.endsWith("Play") });

    this.sounds.push({ key, audio });
  }

  play(key: string, callback: Function = null) {
    return;
    if (!this.mutedMusic.mutedSFX || key == "salawat") {
      const target = this.sounds.find((sound: any) => sound.key === key);

      if (target) {
        target.audio.play();

        if (typeof callback == "function") {
          if (this.current) {
            this.current.volume(0.2);
          }
          target.audio.once("end", () => {
            callback();
            if (this.current) {
              this.current.volume(1);
            }
          });
        }
      }
    }
  }

  stop(key: string) {
    const target = this.sounds.find((sound: any) => sound.key === key);

    if (target) {
      target.audio.stop();
    }
  }

  loop(key: string) {
    if (this.mutedMusic.mutedMusic) {
      if (this.current) {
        this.current.stop();
        this.current = null;
      }
    } else {
      const target = this.sounds.find((sound: any) => sound.key === key);

      if (target) {
        if (this.current) {
          this.current.stop();
        }

        this.current = target.audio;
        this.current.play();
      }
    }
  }

  homeMusic() {
    this.loop("HomePlay");
  }

  gameMusic() {
    this.loop("GamePlay");
  }

  toggleSFXMuteness() {
    this.mutedMusic.mutedSFX = !this.mutedMusic.mutedSFX;
  }

  toggleMusicMuteness() {
    if (!this.mutedMusic.mutedMusic) {
      if (this.current) {
        this.current.stop();
        this.current = null;
      }

      this.mutedMusic.mutedMusic = true;
    } else {
      this.mutedMusic.mutedMusic = false;
      this.loop("HomePlay");
    }
  }

  setMusic(payload) {
    this.mutedMusic.mutedMusic = !payload.music;
    this.mutedMusic.mutedSFX = !payload.sfx;
  }

  getmutedSFX() {
    return this.mutedMusic.mutedSFX;
  }

  getmutedMusic() {
    return this.mutedMusic.mutedMusic;
  }
}
