import { Component, Input } from "@angular/core";
import { DataProvider } from "src/app/services/data";

@Component({
  selector: "star-slot",
  templateUrl: "star-slot.html"
})
export class StarSlotComponent {
  @Input() stars: number;

  readyToPlay = { ready: false };

  constructor(private dataProvider: DataProvider) {
    this.readyToPlay = this.dataProvider.startGame;
  }
}
