import { Component, Input, Output, EventEmitter } from "@angular/core";
import { NavigateService } from "src/app/services/navigate";

@Component({
  selector: "match-item",
  templateUrl: "./match-item.html"
})
export class MatchItemComponent {
  @Input() item: any;
  @Input() type: string;
  @Input() ended: boolean;
  @Input() isHistory: boolean;
  @Input() banCanceledGames: boolean;
  @Output() getHint = new EventEmitter();

  constructor(private Navigate: NavigateService) {}

  goToProfile(id: number) {
    if (id) {
      this.Navigate.profile({ isForMe: false, id });
    }
  }

  goToClanProfile(id: number) {
    if (id) {
      this.Navigate.clanProfile({ id });
    }
  }

  showBanCanceledGamePopup() {
    this.Navigate.presentAlert({ code: "FIRST_ROUND_TIMEOUT" });
  }
}
