import {
  Component,
  Input,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter
} from "@angular/core";
import { Chart } from "chart.js";
import { AbilityTranslator } from "../../../assets/Constants";
import { NavigateService } from "src/app/services/navigate";

@Component({
  selector: "clan-stats",
  templateUrl: "clan-stats.html"
})
export class ClanStatsComponent {
  @ViewChild("pieChart", { static: true }) pieChart: ElementRef;

  @Input() clan: any;
  @Input() history = [];
  @Input() members = [];
  @Input() editable = false;
  @Input() isForMe = false; // if true: show header btns, logs and top players
  @Output() editClanInfo = new EventEmitter();

  chart: Chart;

  abilityTranslator = AbilityTranslator;

  tops = {
    stars: {
      id: 0,
      name: "",
      avatar: "",
      clanStars: 0
    },
    level: {
      id: 0,
      name: "",
      avatar: "",
      level: 0
    },
    brain: {
      id: 0,
      name: "",
      avatar: "",
      brain: { brainScore: { value: 0 } },
      brainScore: 0
    }
  };

  brainBars = [];

  constructor(private Navigate: NavigateService) {}

  ngOnChanges() {
    this.clan.games = this.clan.wins + this.clan.draws + this.clan.loses;
    this.calculateTopPlayers();
    this.calculateBrainBars();
    this.createCharts();
  }

  private calculateBrainBars() {
    const barsDiv = document.getElementById(
      "line-bar-row-" + this.clan.id + "-" + this.isForMe
    );
    if (!barsDiv) {
      setTimeout(() => {
        this.calculateBrainBars();
      }, 100);
      return;
    }
    let max = 1;
    this.brainBars = [];
    Object.keys(this.abilityTranslator).map((key: string) => {
      const clanKey = "stars" + key.charAt(0).toUpperCase() + key.substr(1);
      this.brainBars.push({
        name: this.abilityTranslator[key].substr(0, 9),
        width: 0,
        stars: this.clan[clanKey]
      });
      if (this.clan[clanKey] > max) {
        max = this.clan[clanKey];
      }
    });
    let width = barsDiv.getBoundingClientRect().width - 140;
    this.brainBars.map((bar) => {
      bar.width = ((bar.stars * width) / max).toFixed(0);
    });
    this.brainBars.sort((a, b) => {
      return b.stars - a.stars;
    });
  }

  private calculateTopPlayers() {
    let topsIndex = {
      stars: 0,
      level: 0,
      brain: 0
    };

    this.members.map((member, index) => {
      if (member.clanStars > this.members[topsIndex.stars].clanStars) {
        topsIndex.stars = index;
      }
      if (member.level > this.members[topsIndex.level].level) {
        topsIndex.level = index;
      }
      if (member.brainScore > this.members[topsIndex.brain].brainScore) {
        topsIndex.brain = index;
      }
    });

    if (this.members.length) {
      this.tops.stars = this.members[topsIndex.stars];
      this.tops.level = this.members[topsIndex.level];
      this.tops.brain = this.members[topsIndex.brain];
    }
  }

  createCharts() {
    const pieChartElement = document.getElementById(
      "pie-chart-" + this.clan.id + "-" + this.isForMe
    );
    if (!pieChartElement) {
      setTimeout(() => {
        this.createCharts();
      }, 100);
      return;
    }
    let data = [0, 0, 0];
    if (this.clan.wins != undefined) {
      data = [this.clan.wins, this.clan.loses, this.clan.draws];
    }

    this.chart = new Chart(pieChartElement, {
      type: "pie",
      data: {
        labels: ["Win", "Lose", "Draw"],
        datasets: [
          {
            data,
            backgroundColor: ["#6dd400", "#e02020", "#f7b500"],
            borderColor: "#fff",
            borderWidth: 1
          }
        ]
      },
      options: {
        responsive: true,
        legend: { display: false }
      }
    });
  }

  goToProfile(id: number) {
    this.Navigate.profile({ isForMe: false, id });
  }
}
