"use strict";
import { Component, Input, ViewChild, ElementRef } from "@angular/core";

import { Router } from "@angular/router";
import { ClanService } from "../../services/clan";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "../../services/data";
import { UtilsProvider } from "../../services/utils.service";
import { NavigateService } from "../../services/navigate";
import { SmartAudioProvider } from "src/app/services/smart-audio";
import {
  EventStatuses,
  AbilityTranslator,
  Abilities,
  ClanRoles
} from "../../../assets/Constants";

import { Chart } from "chart.js";
import * as moment from "moment";

@Component({
  selector: "clan-event",
  templateUrl: "clan-event.html"
})
export class ClanEventComponent {
  @ViewChild("ourSideChart", { static: true }) ourChart: ElementRef;
  @ViewChild("theirChart", { static: true }) theirChart: ElementRef;

  event: any;
  @Input() clan: any;
  @Input() games = [];
  @Input() player: any;
  @Input() reward: any;

  chart: Chart;
  cities: any;

  ourEventStats = {
    games: 0,
    stars: 0
  };

  theirEventStats = {
    id: 0,
    flag: "",
    name: "",
    games: 0,
    stars: 0,
    totalStars: 0,
    rank: 1,
    members: 1
  };

  eventStatuses = EventStatuses;

  searching = true; // true: when clan.state == SEARCHING and opponent not found yet
  checkGames = false;
  guide = false; // true: show guides
  scoreboard = {
    show: false,
    active: false,
    players: []
  };
  lastEvent: any;
  lastGame: any; // same last event in good shape

  guidelines = [];

  groupTickets = 0;

  abilitySelected = "agility";
  abilities = Abilities;
  abilityTranslator = AbilityTranslator;

  eventInterval: any;
  abilityStats = {};

  interval = {
    interval: null,
    step: 0,
    timer: 1
  };

  clanRoles = ClanRoles;
  timer: any = null;
  waitingForResult = 1;
  waitingForUpdate = 1;
  waitingForFindingOpponent = 1;

  banCanceledGames = false;

  items = [];

  rewardData = {
    active: false,
    amount: 0,
    we: {
      flag: "",
      name: "",
      members: 0,
      stars: 0,
      after: 0
    },
    they: {
      flag: "",
      name: "",
      members: 0,
      stars: 0
    }
  };

  notPlayingYet = true;

  startGameLoading = false;

  constructor(
    private httpProvider: HttpProvider,
    private dataProvider: DataProvider,
    private clanService: ClanService,
    private utils: UtilsProvider,
    private navigateService: NavigateService,
    private smartAudioProvider: SmartAudioProvider,
    public router: Router
  ) {
    this.clanService.toggleInterval.subscribe((interval: boolean) => {
      this.manageIntervals(interval);
    });
    this.event = this.clanService.currentEvent;
    this.cities = this.dataProvider.getGames().slice(0, 6);
    this.initAbilityStats();
    this.initTheirEventStats();
    this.buildRewardData();
    this.setGuidelines();
  }

  ngOnChanges() {
    this.notPlayingYet = this.player ? this.player.clanStars === 0 : true;
    this.calculateAbilityStats();
    this.manageIntervals(true);
    this.refreshEvent(true);
  }

  private buildRewardData() {
    if (this.reward) {
      this.rewardData.active = true;
      this.rewardData.amount = this.reward.silver;
      const event = this.reward.event;
      const ourSide = event.hostStars > event.guestStars ? "host" : "guest";
      const theirSide = event.hostStars > event.guestStars ? "guest" : "host";
      const we = event[ourSide];
      this.rewardData.we.after = event[ourSide + "StarsAfter"];
      this.rewardData.we.flag = we.flag;
      this.rewardData.we.name = we.name;
      this.rewardData.we.members = we.membersCount;
      this.rewardData.we.stars = event[ourSide + "Stars"];
      const they = event[theirSide];
      this.rewardData.they.flag = they.flag;
      this.rewardData.they.name = they.name;
      this.rewardData.they.members = they.membersCount;
      this.rewardData.they.stars = event[theirSide + "Stars"];
    }
  }

  private manageIntervals(isInterval: boolean) {
    clearInterval(this.interval.interval);
    if (isInterval) {
      this.interval.interval = setInterval(() => {
        this.interval.step += 1;
        if (this.interval.step % this.interval.timer === 0) {
          this.setFinishTime();
        }
        const eve = this.clan.state == this.eventStatuses.SEARCHING ? 5 : 10;
        if (this.interval.step % eve === 0) {
          this.refreshEvent(false);
        }
        this.waitingForUpdate = (((this.interval.step - 1) % eve) + 1) / eve;
        if (this.interval.step >= 2520) {
          this.interval.step = 0;
        }
      }, 987);
    }
  }

  private setFinishTime() {
    if (
      this.clan.state === this.eventStatuses.SEARCHING &&
      this.clan.stateChangedAt
    ) {
      const offset =
        this.dataProvider.getConfig().scales.clan.QUEUE_TIME * 60 * 1000;
      const diff = moment(this.clan.stateChangedAt).diff(moment());
      const time = diff + offset;

      if (time < 0) {
        this.timer = 0;
        setTimeout(() => {
          this.clanService.setClan({
            state: this.eventStatuses.IDLE,
            stateChangedAt: null
          });
        }, 3000);
      } else {
        this.timer = time;
      }
    } else if (this.clan.state == this.eventStatuses.COMPETING) {
      const time = moment(this.event.finishedAt).diff(moment());
      if (time <= 0) {
        this.timer = 0;
        this.waitingForResult = Math.max(0, (time + 60000) / 60000);
      } else {
        this.timer = time;
      }
      this.banCanceledGames =
        time <
        this.dataProvider.getConfig().scales.clan.FINAL_ROUND_TIME * 60 * 1000;
    }
  }

  private calculateScoreboard(event: any) {
    const players = [];
    const sides = this.clanService.getEventSides(event);

    this.games.map((game) => {
      const pid = game[sides.our + "Id"];
      if (pid) {
        let index = players.findIndex((player) => player.id === pid);
        if (index < 0) {
          index = players.length;
          players.push({
            id: pid,
            name: game[sides.our + "Name"],
            games: 0,
            wins: 0,
            draws: 0,
            loses: 0,
            stars: 0
          });
        }
        players[index].games += 1;
        const stars = game[sides.our + "Stars"];
        if (stars !== null) {
          const oppStars = game[sides.their + "Stars"];
          if (stars > oppStars) {
            players[index].wins += 1;
          } else if (stars < oppStars) {
            players[index].loses += 1;
          } else {
            players[index].draws += 1;
          }
          players[index].stars += stars - oppStars;
        }
      }
    });

    players.sort((a, b) => {
      const sd = b.stars - a.stars;
      if (sd === 0) {
        const gd = (a.games - b.games) * Math.sign(a.stars);
        if (gd === 0) {
          return b.wins - a.wins;
        }
        return gd;
      }
      return sd;
    });
    this.scoreboard.players = players;
  }

  private buildGameItems(event: any, reset: boolean) {
    if (reset) {
      this.items = [];
      this.games.sort((a: any, b: any) => a.cityId - b.cityId);
    }
    const sides = this.clanService.getEventSides(event);

    this.games.map((game) => {
      const city = this.cities.find((city: any) => game.cityId === city.id);
      if (
        city &&
        (this.abilitySelected === "all" ||
          this.abilitySelected === city.category)
      ) {
        const playerStar = game[sides.our + "Stars"];
        const star = game[sides.their + "Stars"];
        const state =
          star !== null
            ? star + playerStar === 0
              ? 9
              : Math.sign(playerStar - star) + 7
            : 9;
        const newItem = {
          name: "",
          avatar: "",
          score: 0,
          star,
          opponentId: game[sides.their + "Id"],
          playerName: "",
          playerAvatar: "",
          playerScore: 0,
          playerStar,
          state,
          game: city.info,
          quiz: city.name,
          gameId: game.id
        };
        const keys = ["Name", "Avatar", "Score"];
        keys.map((key) => {
          newItem[key.toLowerCase()] = game[sides.their + key];
          newItem["player" + key] = game[sides.our + key];
        });
        let found = false;
        this.items.map((item) => {
          if (item.quiz === newItem.quiz) {
            this.updateObject(item, newItem);
            found = true;
          }
        });
        if (!found) {
          this.items.push(newItem);
        }
      }
    });
  }

  private initAbilityStats() {
    Object.keys(this.abilityTranslator).map((key: string) => {
      this.abilityStats[key] = {
        stars: 0,
        games: 0,
        color: "",
        value: 0
      };
    });
    this.abilityStats["brainScore"] = { color: "", value: 0 };
  }

  private initTheirEventStats() {
    if (this.event.id) {
      const sides = this.clanService.getEventSides(this.event);
      const they = this.event[sides.their];
      this.theirEventStats.id = this.event[sides.their + "Id"];
      this.theirEventStats.flag = they.flag;
      this.theirEventStats.name = they.name;
      this.theirEventStats.rank = they.rank;
      this.theirEventStats.totalStars = they.stars;
      this.theirEventStats.members = they.membersCount;
    }
  }

  private calculateAbilityStats() {
    if (!this.event.id) {
      return;
    }

    const opponentStars = {};
    const completedGames = {};
    const newStats = {};

    Object.keys(this.abilityTranslator).map((key: string) => {
      opponentStars[key] = 0;
      completedGames[key] = 0;
      newStats[key] = {
        games: 0,
        stars: 0
      };
    });

    const sides = this.clanService.getEventSides(this.event);

    // count games and stars:

    const newOurEventStats = { games: 0, stars: 0 };
    const newTheirEventStats = { games: 0, stars: 0 };
    this.games.map((game) => {
      const city = this.cities.find((city: any) => game.cityId === city.id);
      if (city !== undefined) {
        const category = city.category;
        if (game[sides.our + "Score"] !== null) {
          newStats[category].games += 1;
          if (game[sides.our + "Stars"] !== null) {
            newOurEventStats.stars += game[sides.our + "Stars"];
            newStats[category].stars += game[sides.our + "Stars"];
            completedGames[category] += 1;
          }
          newOurEventStats.games += 1;
        }
        if (game[sides.their + "Score"] !== null) {
          if (game[sides.their + "Stars"] !== null) {
            newTheirEventStats.stars += game[sides.their + "Stars"];
            opponentStars[category] += game[sides.their + "Stars"];
          }
          newTheirEventStats.games += 1;
        }
        if (this.notPlayingYet && game[sides.our + "Id"] === this.player.id) {
          this.notPlayingYet = false;
        }
      }
    });

    this.updateObject(this.ourEventStats, newOurEventStats);
    this.updateObject(this.theirEventStats, newTheirEventStats);

    // calculate colors of brain categories: [0:90]
    Object.keys(this.abilityTranslator).map((key: string) => {
      const count = completedGames[key];
      if (count > 0) {
        const absDiff = newStats[key].stars - opponentStars[key] + count * 3;
        const norm = Math.max(count, Math.min(count * 5, absDiff));
        newStats[key].value = Math.round((22.5 * (norm - count)) / count);
      } else {
        newStats[key].value = 45;
      }
      this.updateObject(this.abilityStats[key], newStats[key]);
    });
  }

  private updateObject(before: any, after: any) {
    Object.keys(after).map((key) => {
      if (before[key] !== after[key]) {
        before[key] = after[key];
      }
    });
  }

  private refreshEvent(start) {
    if (!this.player.clanId) {
      return;
    }
    const body = {
      eventId: this.event.id ? this.event.id : null,
      clanId: this.player.clanId,
      lastGame: !this.lastGame,
      checkReward: start
    };

    this.httpProvider.post(
      "/event/data",
      body,
      (res: any) => {
        if (res.status === "success") {
          if (res.data.tickets) {
            if (this.groupTickets !== res.data.tickets) {
              //todo update members
              this.clanService.needUpdatingMembers.next(true);
              this.groupTickets = res.data.tickets;
            }
          }

          if (res.data.reward) {
            this.reward = res.data.reward;
            this.buildRewardData();
            this.smartAudioProvider.play("win");
          }

          if (res.data.event) {
            if (res.data.games.length) {
              if (this.clan.state === this.eventStatuses.IDLE) {
                this.clanService.setClan({
                  state: this.eventStatuses.SEARCHING,
                  stateChangedAt: res.data.clan.stateChangedAt
                });
                this.timer = null;
              }

              if (
                this.clan.state === this.eventStatuses.SEARCHING &&
                this.searching
              ) {
                this.clanService.setCurrentEvent(res.data.event);
                this.clanService.setClan({ stateChangedAt: null });
                this.searching = false;
              } else if (this.clan.state !== this.eventStatuses.COMPETING) {
                this.clanService.setClan({
                  state: this.eventStatuses.COMPETING
                });
                this.timer = null;
                this.searching = true;
              }
            } else {
              this.clanService.setClan({
                state: this.eventStatuses.SEARCHING,
                stateChangedAt: res.data.clan.stateChangedAt
              });
              this.timer = null;
            }

            if (this.clan.state === this.eventStatuses.COMPETING) {
              this.games = res.data.games;
              this.buildGameItems(this.event, false);
              this.calculateAbilityStats();
              this.calculateScoreboard(this.event);
            } else if (
              this.clan.state === this.eventStatuses.SEARCHING &&
              !this.searching
            ) {
              this.initTheirEventStats();
            }
          } else if (this.clan.state != this.eventStatuses.IDLE) {
            this.clanService.setCurrentEvent({ id: null });
            this.clanService.setClan({
              state: this.eventStatuses.IDLE,
              stateChangedAt: null
            });
            this.clanService.needUpdatingClan.next(true);
          }

          if (res.data.result) {
            this.games = res.data.resultGames;
            this.lastEvent = res.data.result;
            const sides = this.clanService.getEventSides(this.lastEvent);
            const our = this.lastEvent[sides.our];
            const their = this.lastEvent[sides.their];
            const ourStars = this.lastEvent[sides.our + "Stars"];
            const theirStars = this.lastEvent[sides.their + "Stars"];
            this.lastGame = {
              id: this.lastEvent[sides.their + "Id"],
              clanAvatar: our.flag,
              clanName: our.name,
              avatar: their.flag,
              name: their.name,
              state: Math.sign(ourStars - theirStars) + 7,
              score: ourStars + " - " + theirStars,
              getTime: this.utils.setTime(this.lastEvent.finishedAt)
            };
          }
        }
      },
      (err: any) => {}
    );
  }

  showScoreboard(lastGame) {
    if (lastGame) {
      this.calculateScoreboard(this.lastEvent);
    }
    this.scoreboard.show = true;
  }

  startEvent() {
    this.smartAudioProvider.play("buttonClick");

    if (this.player.clanRole > this.clanRoles.MEMBER) {
      this.httpProvider.post(
        "/event/participate",
        { clanId: this.player.clanId },
        (res: any) => {
          if (res.status === "success") {
            this.clanService.setClan({
              state: this.eventStatuses.SEARCHING,
              stateChangedAt: moment()
            });
            this.setFinishTime();
          } else {
            this.navigateService.presentAlert(res.data);
          }
        }
      );
    }
  }

  startGame(helps: number, gameId: number) {
    this.smartAudioProvider.play("buttonClick");
    const body = {
      clanId: this.player.clanId,
      gameId,
      helps
    };

    this.httpProvider.post("/event/start", body, async (res: any) => {
      if (res.status === "success") {
        this.dataProvider.setPlayer({
          tickets: res.data.tickets,
          gem: res.data.gem
        });
        this.clanService.toggleInterval.next(false);
        const item = await this.items.find((item) => item.gameId === gameId);
        item.playerName = this.player.name;
        item.playerAvatar = this.player.avatar;
        this.dataProvider.setGame({
          city: await this.cities.find(
            (city) => city.id == res.data.game.cityId
          ),
          questions: res.data.game.questions,
          gameLevel: 5,
          gameId,
          helps
        });
        this.startGameLoading = true;
        this.httpProvider.post(
          "/event/receipt",
          body,
          (res: any) => {},
          (err: any) => {},
          30000
        );
        await this.router.navigate(["/game"], { state: { war: true } });
        this.startGameLoading = false;
      } else {
        this.navigateService.presentAlert(res.data);
      }
    });
  }

  getHint(gameId) {
    if (this.player.tickets > 0) {
      this.navigateService.presentAlert(
        { code: "GET_YOUR_GUIDES_FOR_WAR" },
        (hints: number) => {
          if (typeof hints != "number") return;
          this.startGame(hints, gameId);
        }
      );
    } else {
      this.addTicket(gameId);
    }
  }

  addTicket(gameId) {
    this.smartAudioProvider.play("buttonClick");
    this.navigateService.presentAlert(
      { code: "GET_TICKET" },
      (data: number) => {
        if (data) this.buyTicket(data, gameId);
      }
    );
  }

  buyTicket(count: number, gameId) {
    this.httpProvider.post("/clan/ticket", { count }, (res: any) => {
      if (res.status === "success") {
        this.dataProvider.setPlayer({
          silver: res.data.silver,
          tickets: res.data.tickets
        });
        if (gameId) {
          this.getHint(gameId);
        }
      } else {
        this.navigateService.presentAlert(res.data);
      }
    });
  }

  showGuide() {
    this.guide = true;
    this.smartAudioProvider.play("buttonClick");
  }

  hideGuide() {
    this.guide = false;
    this.smartAudioProvider.play("buttonClick");
  }

  createCharts(data: any, targetChart: any) {
    const target = targetChart.nativeElement;

    this.chart = new Chart(target, {
      type: "pie",
      data: {
        labels: ["Win", "Lose", "Draw"],
        datasets: [
          {
            data,
            backgroundColor: ["#6dd400", "#e02020", "#f7b500"],
            borderColor: "#fff",
            borderWidth: 1
          }
        ]
      },
      options: {
        responsive: true,
        legend: { display: false }
      }
    });
  }

  selectCategory(lastGame: boolean, ability: string) {
    this.smartAudioProvider.play("buttonClick");
    this.abilitySelected = ability;
    this.checkGames = true;
    this.buildGameItems(lastGame ? this.lastEvent : this.event, true);
  }

  claimReward() {
    this.httpProvider.post("/event/claim", {}, (res: any) => {
      if (res.status === "success") {
        this.smartAudioProvider.play("prize");
        this.dataProvider.setPlayer({ silver: res.data.silver });
        this.rewardData.active = false;
        // this.clanService.needUpdatingClan.next(true)
      }
    });
  }

  goToClanProfile(id: number) {
    if (id) {
      this.navigateService.clanProfile({ id });
    }
  }

  setGuidelines() {
    const config = this.dataProvider.getConfig().scales.clan;
    const limits = this.dataProvider.getConfig().limits.clan;
    this.guidelines = [
      {
        title: "Gift",
        texts: [
          `You can claim ${config.JOIN_GIFT_SILVER} coins and ${config.JOIN_GIFT_GEM} gems the moment you join a team.`
        ]
      },
      {
        title: "Create a team",
        texts: [
          `To create a team you must have at least LVL${limits.create} and pay ${config.CREATE} gems`,
          `To invite members, you have multiple ways: 1) you can tap on someone’s avatar and in their profile page you can hit the “invite to my team” button. 2) In the team members page hit the “invite” button and send invitation to your friends through chat and social media.`
        ]
      },
      {
        title: "Join a team",
        texts: [
          "In the team tab you can search for a team and join it. ",
          "In the chat tab you would receive invitation as a request message and you can join by accepting it."
        ]
      },
      {
        title: "To start a match",
        texts: [
          `Your team must have at least ${config.MINIMUM_MEMBER_FOR_PLAY} members.`,
          "Members must buy tickets to play each quiz.",
          `The team must have at least ${config.MINIMUM_TICKETS} tickets in total to start a match`,
          "The president and deputies can hit the start button and if there is another team ready to play, the match starts"
        ]
      }
    ];
  }
}
