import { Injectable } from '@angular/core';
import { HttpProvider } from './http';
import { DataProvider } from './data';
import { BehaviorSubject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class ClanService {

  clan = {
    id: 0,
    name: '',
    flag: '',
    isPrivate: false,
    description: '',
    requiredLevel: 6,
    stars: 0,
    wins: 0,
    draws: 0,
    loses: 0,
    state: 0,
    starsMath: 0,
    starsMemory: 0,
    starsAgility: 0,
    starsFocus: 0,
    starsImagination: 0,
    starsAccuracy: 0,
    createdAt: '',
    rank: 0,
    stateChangedAt: null
  }

  currentEvent = {
    id: 0,
    scored: false,
    finishedAt: '',
    createdAt: '',
    hostStars: null,
    guestStars: null,
    hostId: 0,
    guestId: 0,
    host: { id: 0, name: '', flag: '' },
    guest: { id: 0, name: '', flag: '' },
  }

  members = []
  history = []
  games = []

  toggleInterval = new BehaviorSubject(false)

  needUpdatingMembers = new BehaviorSubject(false)

  needUpdatingClan = new BehaviorSubject(false)

  constructor(
    private dataProvider: DataProvider,
    private httpProvider: HttpProvider,
  ) { }

  setClan(clan: any) {
    if (!clan) return
    clan.id !== undefined ? this.clan.id = clan.id : ''
    clan.name !== undefined ? this.clan.name = clan.name : ''
    clan.flag !== undefined ? this.clan.flag = clan.flag : ''
    clan.isPrivate !== undefined ? this.clan.isPrivate = clan.isPrivate : ''
    clan.description !== undefined ? this.clan.description = clan.description : ''
    clan.requiredLevel !== undefined ? this.clan.requiredLevel = clan.requiredLevel : ''
    clan.stars !== undefined ? this.clan.stars = clan.stars : ''
    clan.wins !== undefined ? this.clan.wins = clan.wins : ''
    clan.draws !== undefined ? this.clan.draws = clan.draws : ''
    clan.loses !== undefined ? this.clan.loses = clan.loses : ''
    clan.state !== undefined ? this.clan.state = clan.state : ''
    clan.starsMath !== undefined ? this.clan.starsMath = clan.starsMath : ''
    clan.starsMemory !== undefined ? this.clan.starsMemory = clan.starsMemory : ''
    clan.starsAgility !== undefined ? this.clan.starsAgility = clan.starsAgility : ''
    clan.starsFocus !== undefined ? this.clan.starsFocus = clan.starsFocus : ''
    clan.starsImagination !== undefined ? this.clan.starsImagination = clan.starsImagination : ''
    clan.starsAccuracy !== undefined ? this.clan.starsAccuracy = clan.starsAccuracy : ''
    clan.createdAt !== undefined ? this.clan.createdAt = clan.createdAt : ''
    clan.rank !== undefined ? this.clan.rank = clan.rank : ''
    clan.stateChangedAt !== undefined ? this.clan.stateChangedAt = clan.stateChangedAt : ''
  }

  setCurrentEvent(event: any) {
    if (!event) return
    event.id !== undefined ? this.currentEvent.id = event.id : ''
    event.scored !== undefined ? this.currentEvent.scored = event.scored : ''
    event.finishedAt !== undefined ? this.currentEvent.finishedAt = event.finishedAt : ''
    event.hostStars !== undefined ? this.currentEvent.hostStars = event.hostStars : ''
    event.guestStars !== undefined ? this.currentEvent.guestStars = event.guestStars : ''
    event.createdAt !== undefined ? this.currentEvent.createdAt = event.createdAt : ''
    event.hostId !== undefined ? this.currentEvent.hostId = event.hostId : ''
    event.guestId !== undefined ? this.currentEvent.guestId = event.guestId : ''
    event.host !== undefined ? this.currentEvent.host = event.host : ''
    event.guest !== undefined ? this.currentEvent.guest = event.guest : ''
  }

  setHistory(historyItems: any) {
    this.history.splice(0, this.history.length)
    historyItems.map((history: any) => {
      this.history.push(history)
    })
  }

  setMembers(members: any) {
    // first make members empty next add members
    this.members.splice(0, this.members.length)
    members.map((member: any) => {
      this.members.push(member)
    })
  }

  getEventSides(event: any) {
    return {
      our: event.hostId === this.clan.id ? 'host' : 'guest',
      their: event.hostId === this.clan.id ? 'guest' : 'host',
    }
  }

  leaveFromClan(onError: Function) {
    this.httpProvider.post("/clan/leave", {}, (res: any) => {
      if (res.status === "success") {
        this.afterClanExit()
      } else {
        onError(res.data)
      }
    })
  }

  afterClanExit() {
    this.toggleInterval.next(false)
    this.setClan({ id: null })
    this.dataProvider.setPlayer({ clanRole: null, clanId: null })
  }
}
