import { Component, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { NavController, IonSlides, ModalController } from "@ionic/angular";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "../../services/data";
import { SmartAudioProvider } from "../../services/smart-audio";
import { AbilityTranslator } from "../../../assets/Constants";

import { AlertsPage } from "../alerts/alerts";

@Component({
  selector: "page-brain",
  templateUrl: "brain.html"
})
export class BrainPage {
  @ViewChild("slides", { static: false }) slides: IonSlides;

  player;
  games;

  selectedCategory = {
    games: [],
    text: "",
    name: "brainScore"
  };

  brainText = "";
  brainStats = {};
  brainGames = {};

  abilityTranslator = AbilityTranslator;

  platform: string;

  screenShoted = null;
  screenshotMode = false;

  isShowingAlert = false;
  loading = true;

  constructor(
    private router: Router,

    public navCtrl: NavController,

    public modalCtrl: ModalController,
    private smartAudioProvider: SmartAudioProvider,
    private dataProvider: DataProvider,
    private httpProvider: HttpProvider
  ) {
    this.router.navigate([], { queryParams: { modal: "brain" } });
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });
    this.platform = this.dataProvider.platform;
    this.player = this.dataProvider.getPlayer();
    this.games = this.dataProvider.getGames();

    window["backButton"] = () => {
      this.dismiss();
    };
  }

  ionViewWillEnter() {
    this.getData();
  }

  getData() {
    this.httpProvider.get("/users/brain", (res) => {
      this.loading = false;

      if (res.status === "success") {
        this.brainStats = res.data;
        this.setQuizzes();
      } else {
        this.presentAlert(res.data);
      }
    });
  }

  setQuizzes() {
    Object.keys(this.brainStats).map((category) => {
      this.brainGames[category] = this.games.filter(
        (game: any) => game.category == category
      );
    });

    this.changeCategory("brainScore");
  }

  changeCategory(name: string) {
    this.selectedCategory = {
      name,
      text: this.brainStats[name],
      games: this.brainGames[name]
    };
  }

  screenShot() {
    this.screenshotMode = true;

    if (this.platform == "APP") {
      setTimeout(() => {
        // this.screenshot.URI(100).then((res) => {
        //   this.screenShoted = res.URI;
        //   this.showScreenshot(res.URI);
        // });
      }, 500);
    }
  }

  showScreenshot(src) {
    this.screenshotMode = false;
    if (document.getElementById("image_for_share")) {
      document.getElementById("image_for_share").remove();
    }
    var element = document.getElementById("capture");
    var child = document.getElementById("capture-button");

    let image = new Image();
    image.src = src;
    image.id = "image_for_share";

    element.insertBefore(image, child);
    element.classList.remove("d-none");
  }

  shareBrain() {
    document.getElementById("capture").className += " d-none";
    if (document.getElementById("image_for_share")) {
      document.getElementById("image_for_share").remove();
    }

    this.loading = true;

    const image = this.screenShoted;
    const text = " ";
    let link =
      "https://play.google.com/store/apps/details?id=com.harakaquiz.harakaquiz";

    // this.socialSharing.share(text, null, image, link).then(() => {
    //   this.loading = false;
    // });
  }

  showQuizAtTraining(gameId) {
    this.smartAudioProvider.play("buttonClick");
    if (this.player.games > 5) {
      this.dismiss();
      this.navCtrl.setDirection("root");
      this.router.navigate(["tabs/training"], { state: { gameId } });
    }
  }

  async presentAlert(payload) {
    if (!this.isShowingAlert) {
      let prizeModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      prizeModal.present();
    }
  }

  dismiss(screenshotMode = false) {
    this.smartAudioProvider.play("back");
    if (screenshotMode) {
      document.getElementById("capture").className += " d-none";
      if (document.getElementById("image_for_share")) {
        document.getElementById("image_for_share").remove();
      }
      return;
    }
    this.router.navigate([], { queryParams: {} });

    this.modalCtrl.dismiss();
  }
}
