import { Component } from "@angular/core";
import { NavController, ModalController } from "@ionic/angular";
import { SmartAudioProvider } from "../../services/smart-audio";
import { AlertsPage } from "../../pages/alerts/alerts";
import { DataProvider } from "../../services/data";
import { HttpProvider } from "../../services/http";
import { RewardPage } from "../reward/reward";

@Component({
  selector: "page-map",
  templateUrl: "map.html"
})
export class MapPage {
  minHeight = 1200;
  player = { trophy: 0, games: 4 };

  trophyBarPercent = 0;

  countries = [
    {
      name: "Coming Soon",
      enName: "null",
      trophy: 9999999,
      src: "../../assets/imgs/map/map-null.png",
      first: 0,
      second: 0
    }
  ];

  // to set animation on onboarding

  roadResponse = {
    najaf: {},
    karbala: {},
    mecca: {},
    medina: {},
    mashhad: {}
  };

  isShowingAlert = false;
  loading = false;

  constructor(
    public navCtrl: NavController,
    private SmartAudioProvider: SmartAudioProvider,
    private modalCtrl: ModalController,
    private httpProvider: HttpProvider,
    private dataProvider: DataProvider
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });

    // get countries from back and add last country knows as coming soon that defined top of page
    this.countries = [
      ...this.dataProvider.getCountries().sort((a, b) => a.trophy - b.trophy),
      ...this.countries
    ];

    this.minHeight = (this.countries.length - 1) * 500 + 220;
    this.player = this.dataProvider.getPlayer();

    setTimeout(() => {
      document.getElementsByClassName("start-icon")[0].scrollIntoView();
    }, 100);

    this.setIslands();
    this.getAndSetData();
  }

  setIslands() {
    const myIsland = this.countries.find(
      (con) => con.trophy > this.player.trophy
    );

    this.countries.map((country, index) => {
      if (country.trophy < myIsland.trophy) {
        // past island
        country["mapLock"] = false;
        this.trophyBarPercent += 500;
      } else if (country.trophy > myIsland.trophy) {
        // next island
        country["mapLock"] = true;
      } else {
        // current island
        country["mapLock"] = false;
        index === 0
          ? (this.trophyBarPercent +=
              (this.player.trophy / country.trophy) * 500)
          : (this.trophyBarPercent +=
              ((this.player.trophy - this.countries[index - 1].trophy) /
                (country.trophy - this.countries[index - 1].trophy)) *
              500);
      }
      country["firstButtom"] =
        index * 500 +
        (index > 0
          ? ((country.first - this.countries[index - 1].trophy) /
              (country.trophy - this.countries[index - 1].trophy)) *
            500
          : (country.first / country.trophy) * 500);
      country["secondButtom"] =
        index * 500 +
        (index > 0
          ? ((country.second - this.countries[index - 1].trophy) /
              (country.trophy - this.countries[index - 1].trophy)) *
            500
          : (country.second / country.trophy) * 500);

      // set images

      country["mapSrc"] =
        "../../assets/imgs/map/map-" + country.enName + ".png";
    });
  }

  getAndSetData() {
    this.httpProvider.get("/users/road", (res) => {
      if (res.status == "success") {
        this.roadResponse = res.data.chests;
      }
    });
  }

  async collectChest(island, trophy, state) {
    if (this.roadResponse[island][state]) {
      this.SmartAudioProvider.play("chest");

      const modal = await this.modalCtrl.create({
        component: RewardPage,
        componentProps: { source: 1, rewardType: "CHEST", trophy }
      });
      modal.present();
      modal.onDidDismiss().then((payload) => {
        if (payload.data) {
          this.roadResponse[island][state] = 2;
        }
      });
    } else {
      this.presentAlert({ code: "LOW_TROPHY" });
    }
  }

  selectIsland(island) {
    this.SmartAudioProvider.play("buttonClick");
    this.modalCtrl.dismiss({ setIsland: island });
  }

  async presentAlert(payload) {
    if (!this.isShowingAlert) {
      const prizeModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      prizeModal.present();
    }
  }

  dismiss() {
    this.SmartAudioProvider.play("back");

    if (this.dataProvider.getBadges().map) {
      let map = false;
      Object.values(this.roadResponse).map((road) => {
        Object.values(road).map((status) => {
          if (status == 1) map = true;
        });
      });
      this.dataProvider.setBadges({ map });
    }

    this.modalCtrl.dismiss();
  }
}
