import { Component, Input } from "@angular/core";
import { SmartAudioProvider } from "../../services/smart-audio";
import { DataProvider } from "../../services/data";
import { NavigateService } from "../../services/navigate";

@Component({
  selector: "player-info",
  templateUrl: "player-info.html"
})
export class PlayerInfoComponent {
  @Input() player: any;
  @Input() games: any;

  readyToPlay: any;

  radius = null;
  style: any;

  constructor(
    private SmartAudioProvider: SmartAudioProvider,
    private Navigate: NavigateService,
    private dataProvider: DataProvider
  ) {
    this.readyToPlay = this.dataProvider.startGame;
  }

  ngOnInit() {
    setTimeout(() => this.setupXpBarPosition(), 1500);
  }

  ngOnChanges() {
    setTimeout(() => this.setupXpBarPosition(), 1500);
  }

  setupXpBarPosition() {
    var element = document
      .getElementById("home-avatar-component")
      .getBoundingClientRect();

    if (element.height) {
      this.radius = null;
      this.radius = (element.height * 0.5).toFixed(1) + 10;
      this.style = {
        top: +element.top.toFixed(1) - 10 + "px"
      };
    } else {
      setTimeout(() => this.setupXpBarPosition(), 500);
    }
  }

  async goToProfile() {
    if (this.readyToPlay.ready) {
      return;
    }

    // this.SmartAudioProvider.play("trophy");
    // this.Navigate.profile({ isForMe: true });
  }

  async goToOthers(activeTab: number) {
    if (this.readyToPlay.ready) {
      return;
    }

    // this.SmartAudioProvider.play("trophy");
    // this.Navigate.others({ activeTab });
  }
}
