import { Component, Input } from "@angular/core";

@Component({
  selector: "loading-loading",
  templateUrl: "loading-loading.html"
})
export class LoadingLoadingComponent {
  @Input() animationName = "loading";
  @Input() text: String = "empty";
  @Input() color: String;

  anim: any;

  loading = {
    loop: true,
    autoplay: true,
    renderer: "svg",
    path: "../assets/animations/loading.json"
  };

  animations = {
    hourglass: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/hourglass.json"
    },
    salawat: {
      loop: false,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/salawat.json"
    },

    birds: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/birds.json"
    },

    namaz: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/namaz.json"
    },

    celebration: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/celebration.json"
    },

    ENERGYPACK: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/beatHeart.json"
    },

    badge: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/badge.json"
    },

    chest: {
      loop: false,
      autoplay: false,
      renderer: "svg",
      path: "../assets/animations/chest.json"
    },

    comingsoon: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/comingsoon.json"
    },

    building: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/building.json"
    },

    construction: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/construction.json"
    },

    trophy: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/trophy.json"
    },

    star: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/star.json"
    },

    changeIsland: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/changeIsland.json"
    },

    limitAlert: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/limitAlert.json"
    },

    typing: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/chat.json"
    },

    chat: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/chatList.json"
    },

    finding: {
      loop: true,
      autoplay: true,
      renderer: "svg",
      path: "../assets/animations/finding.json"
    }
  };

  constructor() {}

  handleAnimation(anim: any) {
    this.anim = anim;
    if (this.animationName == "loading") this.speedUpAnimation(2);
    if (this.animationName == "ENERGYPACK") this.speedUpAnimation(0.7);
  }

  speedUpAnimation(speed) {
    this.anim.setSpeed(speed);
  }

  animationClick() {
    if (this.animationName == "chest") this.anim.stop(), this.anim.play();
  }
}
