import { Component, Input, Output, EventEmitter } from '@angular/core';
import { UtilsProvider } from 'src/app/services/utils.service';


@Component({
  selector: 'chat-item',
  templateUrl: './chat-item.component.html',
})
export class ChatItemComponent {

  @Input() chat: any
  @Input() editMode: boolean

  @Output() onBlock = new EventEmitter()
  @Output() onRemove = new EventEmitter()

  duration = 5000
  removing = false

  constructor(private Utils: UtilsProvider) { }

  action(type: string) {
    event.stopPropagation()
    
    if (this.removing) { return }
    this.removing = true
    
    const duration = type == "remove" ? this.duration : 2500
    const message = type == "remove" ? `You’re removing ${ this.chat.name }’s chat` : `You’re blocking ${ this.chat.name }`
    const buttons = [{
          text: 'Cancel',
          role: 'cancel'
        }]
    this.Utils.presentToast(message, buttons, "bottom", duration, null, (role: string) =>  {
      this.toastHandle(role, type, this.chat.id)
    })
  }

  toastHandle(role: string, type: string, id: number) {
    if (role == "timeout") {
      if (type == "block") {
        this.onBlock.emit(id)
      } else {
        this.onRemove.emit(id)
      }
    }
      
    this.removing = false
  }
}
