import { Component, Input } from "@angular/core";
import { HttpProvider } from "src/app/services/http";
import { ClanRoles } from "../../../assets/Constants";
import { NavigateService } from "src/app/services/navigate";
import { SmartAudioProvider } from "src/app/services/smart-audio";
import { ClanService } from "src/app/services/clan";
import { DataProvider } from "src/app/services/data";

@Component({
  selector: "clan-members",
  templateUrl: "clan-members.html"
})
export class ClanMembersComponent {
  @Input() members = [];
  @Input() managable = false;
  @Input() playerId: number;
  @Input() playerRole: number;

  manageMode = false;
  filter = "brainScore";

  clanRoles = ClanRoles;
  // list of ids for manage clan
  kickedMembers = [];
  promotedMembers = [];
  unpromotedMembers = [];

  constructor(
    private ClanService: ClanService,
    private smartAudioProvider: SmartAudioProvider,
    private Navigate: NavigateService,
    private httpProvider: HttpProvider
  ) {}

  sortMembers(by: string) {
    this.smartAudioProvider.play("slide");
    this.filter = by;
    this.members.sort((a: any, b: any) => b[by] - a[by]);
  }

  manage() {
    this.smartAudioProvider.play("buttonClick");
    if (this.managable) {
      this.manageMode = true;
    }
  }

  removeMember(id: number) {
    this.smartAudioProvider.play("change");
    if (this.kickedMembers.includes(id)) {
      this.kickedMembers.splice(this.kickedMembers.indexOf(id), 1);
    } else {
      this.kickedMembers.push(id);
    }
  }

  promoteMemeber(member: any) {
    this.smartAudioProvider.play("change");
    if (this.unpromotedMembers.includes(member.id)) {
      this.unpromotedMembers.splice(
        this.unpromotedMembers.indexOf(member.id),
        1
      );
      this.promotedMembers.push(member.id);
    } else if (this.promotedMembers.includes(member.id)) {
      this.promotedMembers.splice(this.promotedMembers.indexOf(member.id), 1);
      if (member.clanRole == this.clanRoles.CO_LEADER) {
        this.unpromotedMembers.push(member.id);
      }
    } else if (member.clanRole == this.clanRoles.CO_LEADER) {
      this.unpromotedMembers.push(member.id);
    } else {
      this.promotedMembers.push(member.id);
    }
  }

  submitChanges() {
    this.smartAudioProvider.play("submit");

    const body = {
      kickedMembersId: this.kickedMembers,
      promotedMembersId: this.promotedMembers,
      unpromotedMembersId: this.unpromotedMembers
    };

    this.promotedMembers = [];
    this.unpromotedMembers = [];
    this.kickedMembers = [];

    this.manageMode = false;
    if (
      [
        ...body.kickedMembersId,
        ...body.promotedMembersId,
        ...body.unpromotedMembersId
      ].length
    ) {
      this.httpProvider.post("/clan/manage", body, (res: any) => {
        if (res.status === "success") {
          this.members.map((member, index, all) => {
            if (body.kickedMembersId.includes(member.id)) {
              all.splice(index, 1);
            } else if (body.promotedMembersId.includes(member.id)) {
              member.clanRole = this.clanRoles.CO_LEADER;
            } else if (body.unpromotedMembersId.includes(member.id)) {
              member.clanRole = this.clanRoles.MEMBER;
            }
          });
          this.manageMode = false;
        } else {
          this.Navigate.presentAlert(res.data);
        }
      });
    }
  }

  leaveClan() {
    this.Navigate.presentAlert(
      { code: "CONFIRM_LEAVE_CLAN" },
      (data: boolean) => {
        if (data) {
          this.ClanService.leaveFromClan((payload: any) => {
            this.Navigate.presentAlert(payload);
          });
        }
      }
    );
  }

  goToProfile(id: number) {
    this.Navigate.profile({
      isForMe: this.playerId == id,
      id
    });
  }

  inviteToGroup() {
    this.smartAudioProvider.play("buttonClick");

    const text = `I am in ${this.ClanService.clan.name} team. Download Haraka now to play next to each other.\n\n`;
    const link =
      "https://play.google.com/store/apps/details?id=com.harakaquiz.harakaquiz";

    // this.socialSharing.share(text, null, null, link);
  }
}
