import { Component, Input } from '@angular/core';
import { NavigateService } from '../../services/navigate';
import { HttpProvider } from '../../services/http';

@Component({
  selector: 'messenger',
  templateUrl: './messenger.html',
})
export class MessengerComponent {

  @Input() messages: any
  @Input() playerId: any

  constructor(
    private navigateService: NavigateService,
    private httpProvider: HttpProvider,
  ) {
  }

  goToClanProfile(id) {
    if (id) {
      this.navigateService.clanProfile({ id })
    }
  }

  goToProfile(id) {
    if (id && id !== 16) {
      this.navigateService.profile({
        isForMe: this.playerId == id,
        id
      })
    }
  }

  reject(id) {
    this.httpProvider.post('/chat/deleteMessage', { messageId: id }, (res: any) => {
      if (res.status === "success") {
        const index = this.messages.chats.findIndex(message => message.id === id)
        if (index >= 0) {
          this.messages.chats.splice(index, 1)
        }
      }
    })
  }
}
