import { Component } from "@angular/core";
import { NavController, ModalController } from "@ionic/angular";
import { DataProvider } from "../../services/data";
import { HttpProvider } from "../../services/http";
import { NavigateService } from "src/app/services/navigate";
import { SmartAudioProvider } from "../../services/smart-audio";
import { AlertsPage } from "../alerts/alerts";

@Component({
  selector: "page-reward",
  templateUrl: "reward.html"
})
export class RewardPage {
  player;

  // for show reward and change king, chest and show animations
  collected: boolean = false;

  rewards = [];

  // sources // TROPHY: 1, HAND: 2, RETENTION: 3
  rewardType: string = "CHEST"; // CHEST, LEVEL, MINIGAME, STATE, LIMIT
  source: number;
  trophy: number;

  payload;

  mobile = null;
  // defined for finger game (MINIGAME)
  gameProgress = 0;
  gameAmount = 10;

  // rubikaLimit = 5;
  // rubikaLimitVersion = 1;

  showShopButton = false;

  isShowingAlert = false;

  requestSuccess = false;

  constructor(
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    private smartAudioProvider: SmartAudioProvider,
    private Navigate: NavigateService,
    private httpProvider: HttpProvider,
    private dataProvider: DataProvider
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });
    this.player = this.dataProvider.getPlayer();
    this.mobile = this.player.mobile;
  }

  async ngOnInit() {
    if (
      this.rewardType == "LEVEL" ||
      this.rewardType == "STATE"
      // this.rewardType == "LIMIT"
    ) {
      this.collected = true;
      if (this.rewardType == "LEVEL") {
        this.smartAudioProvider.play("levelUp");
      }
      if (this.rewardType == "STATE") {
        this.smartAudioProvider.play("islandUnlock");
      }
      // if (this.rewardType == "LIMIT") {
      //   const storedVersion = await this.dataProvider.getFromStorage(
      //     "rubika-limit-version"
      //   );

      //   if (storedVersion == 1) {
      //     this.rubikaLimitVersion = 2;
      //     this.dataProvider.setTostorage("rubika-limit-version", 2);
      //   } else {
      //     this.dataProvider.setTostorage("rubika-limit-version", 1);
      //   }

      //   this.collected = false;
      //   this.smartAudioProvider.play("noMoney");
      // }
    } else {
      this.getData(this.source, this.trophy);
    }
  }

  getData(source: number, trophy = undefined) {
    const body = { source, trophy };
    this.httpProvider.post("/users/chest", body, (res: any) => {
      if (res.status === "success") {
        this.smartAudioProvider.play("achive");
        this.rewards = res.data.rewards;
        this.requestSuccess = true;
        this.useRewards(res.data.rewards);
      }
    });
  }

  useRewards(rewards: any) {
    rewards.map((item: any) => {
      if (item.type == "GOLD") {
        this.dataProvider.setPlayer({
          silver: this.player.silver + item.value
        });
      }
    });
  }

  chestClicked() {
    this.smartAudioProvider.play("chest");
    this.gameProgress += 1;
    this.gameProgress >= this.gameAmount ? (this.collected = true) : "";
  }

  async presentAlert(payload) {
    if (!this.isShowingAlert) {
      let prizeModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      prizeModal.present();
      prizeModal.onDidDismiss().then((data) => {
        if (data.data && payload.code == 100) {
          this.reviewInStore();
        }
      });
    }
  }

  reviewInStore() {
    // ToDo: connect to google play review
  }

  async goToShop() {
    this.dismiss();
    if (this.showShopButton) {
      this.Navigate.shop();
    }
  }

  dismiss() {
    this.smartAudioProvider.play("submit");

    if (this.requestSuccess && this.source == 1) {
      this.modalCtrl.dismiss(true);
    } else {
      this.modalCtrl.dismiss();
    }
  }

  async ionViewDidLeave() {
    // const reviewClients = [
    //   this.dataProvider.CLIENTS.BAZAAR,
    //   this.dataProvider.CLIENTS.MYKET,
    // ];
    // if (reviewClients.includes(this.dataProvider.client)) {
    //   if (
    //     this.rewardType == "LEVEL" &&
    //     this.player.level >= 3 &&
    //     !(await this.dataProvider.getFromStorage("review"))
    //   ) {
    //     this.presentAlert({ code: 100 });
    //   }
    // }
  }
}
