import { Component, Input } from '@angular/core';


@Component({
  selector: 'avatar',
  templateUrl: 'avatar.html'
})
export class AvatarComponent {

  @Input() avatars: string = null
  @Input() level = 0
  @Input() flag = null

  @Input() star = null
  voidStar: object

  locations = ["nullman"]

  constructor() {}

  ngOnChanges(){    
    if (this.avatars) { this.locations = this.avatars.split("-") }

    if (this.star !== null) {
      this.voidStar = Array(3 - this.star)
      this.star = Array(this.star)
    }
  }
}
