import { Component } from "@angular/core";
import { NavController, ModalController } from "@ionic/angular";
import { SmartAudioProvider } from "../../services/smart-audio";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "../../services/data";
import { NavigateService } from "../../services/navigate";

import { RewardPage } from "../reward/reward";

@Component({
  selector: "page-gifts",
  templateUrl: "gifts.html"
})
export class GiftsPage {
  registerReward = 0; // show 150 silver for create account when player doesn't set mobile number
  inviteReward = 200;
  retentionRewards: any;

  player: any;

  retentionQuest = { progress: 0, active: true };
  trophyQuest: any;
  brainQuest: any;
  dailyQuests: any;

  giftCode: any;
  platform: string;

  logoImageData: string; //  base 64 of logo add for sharing
  isShowingAlert = false;
  loading = false;

  changing = false;

  constructor(
    private smartAudioProvider: SmartAudioProvider,
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    private httpProvider: HttpProvider,
    private navigateService: NavigateService,
    private dataProvider: DataProvider
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });

    this.platform = this.dataProvider.platform;
    this.player = this.dataProvider.getPlayer();

    const quests = this.dataProvider.getQuests();
    this.retentionQuest = quests.retention;
    this.trophyQuest = quests.trophy;
    this.brainQuest = quests.brain;
    this.dailyQuests = quests.daily;

    const configs = this.dataProvider.getConfig().scales;
    this.retentionRewards = configs.retentionRewards;
    this.inviteReward = configs.myQmarz.invite;
    this.registerReward += this.player.mobile ? 0 : configs.myQmarz.setMobile;
    this.registerReward += this.player.province
      ? 0
      : configs.myQmarz.setProvince;

    if (this.dataProvider.getBadges().quest) {
      this.dataProvider.setBadges({ quest: false });
    }
  }

  retentionChecked() {
    if (this.retentionQuest.active) {
      this.smartAudioProvider.play("chest");
      if (this.retentionQuest.progress < 7) {
        this.retentionQuest = {
          progress: this.retentionQuest.progress + 1,
          active: false
        };

        if (this.retentionQuest.progress == 7) {
          this.openRetentionChest();
        } else {
          const body = {};
          this.httpProvider.post("/quest/attend", body, (res) => {
            if (res.status == "success") {
              this.dataProvider.setPlayer({ silver: res.data.silver });
              this.dataProvider.setQuests({
                retentionQuest: this.retentionQuest
              });
            }
          });
        }
      }
    }
  }

  skipBrainQuest() {
    this.changing = true;
    this.httpProvider.post("/quest/skip", {}, async (res) => {
      if (res.status === "success") {
        this.dataProvider.setQuests(res.data.quests);
        this.brainQuest = res.data.quests.brainQuest;
        this.dataProvider.setPlayer({ gem: res.data.user.gem });
      } else {
        this.navigateService.presentAlert(res.data);
      }
      this.changing = false;
    });
  }

  async openRetentionChest() {
    const modal = await this.modalCtrl.create({
      component: RewardPage,
      componentProps: { source: 3 }
    });
    modal.present();

    this.dataProvider.setQuests({ retentionQuest: this.retentionQuest });
  }

  collectQuest(quest: any, progress: number) {
    if (!quest.claimed && progress >= quest.goal) {
      this.smartAudioProvider.play("buttonClick");
      this.loading = true;

      const field = quest.field ? quest.field : quest.type;

      this.httpProvider.post("/quest/claim", { field }, (res: any) => {
        this.loading = false;

        if (res.status === "success") {
          this.smartAudioProvider.play("achive");
          this.updateQuest(quest.reward, field);
        }
      });
    }
  }

  updateQuest(reward: any, field: string) {
    // set claimed true, use reward
    if (field == "trophy") {
      this.trophyQuest = { ...this.trophyQuest, claimed: true };
      this.dataProvider.setQuests({ trophyQuest: this.trophyQuest });
    } else if (field == "CLEAN_WIN" || field == "WIN" || field == "QUIZ") {
      const quest = this.dailyQuests.find((quest) => quest.type == field);
      const questIndex = this.dailyQuests.indexOf(quest);

      this.dailyQuests[questIndex] = {
        ...this.dailyQuests[questIndex],
        claimed: true
      };
      this.dataProvider.setQuests({ dailyQuests: this.dailyQuests });
    } else {
      // set quest claimed
      this.brainQuest = { ...this.brainQuest, claimed: true };
      this.dataProvider.setQuests({ brainQuest: this.brainQuest });
    }

    this.useReward(reward);
  }

  useReward(reward: any) {
    if (reward.type == "GOLD") {
      this.dataProvider.setPlayer({
        silver: this.player.silver + reward.value
      });
    }

    if (reward.type == "AVATAR") {
      this.dataProvider.setBadges({ profile: true });
    }

    if (
      reward.type == "DISCOUNT-ACCELERATOR" ||
      reward.type == "DISCOUNT-ENERGYPACK"
    ) {
      this.dataProvider.setBadges({ shop: true });
    }
  }

  dismiss(data = null) {
    this.modalCtrl.dismiss(data);
    this.smartAudioProvider.play("back");
  }
}
