import { Component } from "@angular/core";
import {
  NavController,
  ModalController,
  ToastController
} from "@ionic/angular";
import { Router } from "@angular/router";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "../../services/data";
import { UtilsProvider } from "../../services/utils.service";
import { SmartAudioProvider } from "../../services/smart-audio";
import { AlertsPage } from "../alerts/alerts";
import { Provinces } from "../../../assets/Constants";
import { ClanService } from "src/app/services/clan";

@Component({
  selector: "page-profile",
  templateUrl: "profile.html"
})
export class ProfilePage {
  player: any;
  clan: any;
  restore = false;
  userId;
  clanId;
  activeTab = 0; /// 0 = myAvatar, 1 = qmarz ID
  isForMe = true;
  clanProfile = false;

  activeLocation;
  firstActiveLocation = {};

  base;
  avatar;
  avatarElements;

  locationCosts = {};
  locationLevels = {};

  disableAvatarSubmit = false;

  discount;
  avatarPrice = 0;
  discountedPrice = 0;

  informationCosts; // editName, editMobile, editProvince, setMobile, setProvince
  editCost = 0;
  username;
  mobile;
  province;
  inputIsAvailable: boolean = true;
  submitError: string = "";

  iranProviences;
  client;
  platform;

  isShowingAlert = false;
  loading = false;
  inputLoading: boolean = false;

  id;

  keybordIsOpen;

  invited = false;

  constructor(
    public modalCtrl: ModalController,
    private toastCtrl: ToastController,
    public navCtrl: NavController,
    private router: Router,
    private http: HttpProvider,
    private dataProvider: DataProvider,
    private SmartAudioProvider: SmartAudioProvider,
    private clanService: ClanService,
    private utils: UtilsProvider
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });
    this.informationCosts = this.dataProvider.getConfig().scales.myQmarz;

    this.player = this.dataProvider.getPlayer();
    this.base = this.player.avatar;
    this.avatar = this.base;
    this.userId = this.player.id;
    this.clanId = this.player.clanId;
    this.username = this.player.name;
    this.mobile = this.player.mobile ? this.player.mobile : "";
    this.province = this.player.province;

    this.client = dataProvider.client;
    this.platform = dataProvider.platform;
    this.iranProviences = Provinces;
  }

  ngOnInit() {
    if (this.clanProfile) {
      this.getClanData(this.id);
    } else if (!this.isForMe) {
      this.getUserData(this.id);
    } else {
      this.clan = this.clanService.clan;
      this.getBarberShopElement();
    }
  }

  getUserData(id: number) {
    this.loading = true;
    const profile = "/users/profile/" + id;

    this.http.get(profile, (res) => {
      this.player = { ...this.player, ...res.data };
      if (res.data.clan) {
        this.clan = { ...this.clan, ...res.data.clan };
        this.player.clanId = this.clan.id;
      } else {
        this.player.clanId = null;
      }
      this.loading = false;
      this.activeTab = 1;
    });
  }

  getClanData(id: number) {
    this.loading = true;
    const body = { clanId: id };
    this.http.post("/clan/profile/", body, (res: any) => {
      this.clan = { ...this.clan, ...res.data.profile };
      this.loading = false;
      this.activeTab = 1;
    });
  }

  inviteToMyClan(guestId) {
    this.SmartAudioProvider.play("buttonClick");
    this.http.post("/clan/invite", { guestId }, (res: any) => {
      if (res.status === "success") {
        this.utils.presentToast("Invitation sent successfully.");
        this.invited = true;
      } else {
        this.presentAlert(res.data);
      }
    });
  }

  joinToClan() {
    if (this.player.clanId == this.id || !this.clanProfile || !this.id) return;

    this.SmartAudioProvider.play("buttonClick");
    this.loading = true;
    const body = { clanId: this.id };
    this.http.post("/clan/join", body, (res: any) => {
      this.loading = false;
      if (res.status === "success") {
        this.dataProvider.setPlayer({
          clanId: res.data.clan.id,
          clanRole: 0, // CLanRoles.MEMBER is 0
          gem: res.data.gem,
          silver: res.data.silver
        });
        this.goToClanPage();
        this.clanService.needUpdatingClan.next(true);
      } else {
        this.presentAlert(res.data);
      }
    });
  }

  getBarberShopElement() {
    this.http.get("/avatars", (res) => {
      if (res.status === "success") {
        this.discount = res.data.discount;
        this.avatarElements = res.data.avatars;

        this.avatarElements.map((element) => {
          this.firstActiveLocation[element.priority] = element.startIndex;
          element.items.map((item) => {
            this.locationCosts[item.location] = item.cost;
            this.locationLevels[item.location] = item.level;
          });
        });
        this.activeLocation = { ...this.firstActiveLocation };

        this.loading = false;
      }
    });
  }

  resetAvatar() {
    this.SmartAudioProvider.play("buttonClick");
    this.avatar = this.base;
    this.activeLocation = { ...this.firstActiveLocation };
    this.avatarPrice = 0;
    this.discountedPrice = 0;
  }

  nextPrevElement(direction, items, priority) {
    this.SmartAudioProvider.play("slide");
    if (
      this.activeLocation[priority] + 1 < items.length &&
      direction == "next"
    ) {
      this.activeLocation[priority] += 1;
    } else if (this.activeLocation[priority] && direction == "prev") {
      this.activeLocation[priority] -= 1;
    }

    this.changeAvatar(items[this.activeLocation[priority]]);
  }

  changeAvatar(element) {
    let arrAvatar = this.avatar.split("-");
    this.avatarPrice = 0;
    this.disableAvatarSubmit = false;

    // find index of layer that need change
    let index = arrAvatar.findIndex((el) => {
      return el.substring(3, 5) == element.priority;
    });
    // rewrite element on layer or push too avaatar if does not exist
    index === -1
      ? arrAvatar.push(element.location)
      : (arrAvatar[index] = element.location);
    // sort new array
    arrAvatar.sort((a, b) => {
      return parseInt(a.substring(3, 5)) - parseInt(b.substring(3, 5));
    });

    arrAvatar.map((location) => {
      if (!this.base.includes(location)) {
        if (this.locationLevels[location] > this.player.level) {
          this.disableAvatarSubmit = true;
        }
        this.avatarPrice += this.locationCosts[location];
      }
    });

    this.discountedPrice = Math.round(
      this.avatarPrice - (this.discount * this.avatarPrice) / 100
    );
    this.avatar = arrAvatar.join("-");
  }

  async submitAvatar() {
    if (this.disableAvatarSubmit) {
      return;
    }

    if (this.dataProvider.getPlayer().silver < this.discountedPrice) {
      const payload = {
        code: "LOW_MONEY",
        amount: this.discountedPrice - this.dataProvider.getPlayer().silver
      };
      this.presentAlert(payload);
      return;
    }

    let emptyElement = [
      "-0000200",
      "-0000300",
      "-0000600",
      "-0001000",
      "-0001100",
      "-0001200"
    ];
    emptyElement.map((el) => {
      this.avatar = this.avatar.replace(el, "");
    });

    this.SmartAudioProvider.play("submit");
    this.http.post("/avatars/edit", { avatar: this.avatar }, async (res) => {
      if (res.status === "success") {
        this.dataProvider.setPlayer({
          avatar: this.avatar,
          silver: this.player.silver - this.discountedPrice
        });

        this.dismiss();
      } else {
        this.presentAlert(res.data);
      }
    });
  }

  checkInput(e: any, input: string) {
    if (input == "username" && this.username == this.player.name) {
      this.submitError = "";
      this.changeInfo();
      return;
    }

    this.inputLoading = true;

    const isAvailable = this.utils.checkInput(e, input);

    isAvailable(
      (res: any) => {
        setTimeout(() => {
          this.inputLoading = false;
        }, 100);
        if (res.status === "success") {
          this.inputIsAvailable = res.data.available;
          if (this.inputIsAvailable) {
            this.submitError = "";
            this.changeInfo();
          } else if (input === "email") {
            this.submitError = "Email address already submited!";
          } else {
            this.submitError = "Username already taken!";
          }
        }
      },
      (err) => {
        this.inputLoading = false;
      }
    );
  }

  changeInfo() {
    if (this.informationCosts) {
      this.editCost = 0;

      if (this.username.trim().toUpperCase() != this.player.name) {
        if (this.username.length < 3) {
          this.username = this.player.name.substring(0, 3);
        }
        this.editCost = -this.informationCosts.editName;
      }

      if (this.mobile != "" && this.mobile != this.player.mobile)
        this.player.mobile
          ? (this.editCost -= this.informationCosts.editMobile)
          : (this.editCost += this.informationCosts.setMobile);

      if (this.province != this.player.province)
        this.player.province && this.player.province !== "United Kingdom"
          ? (this.editCost -= this.informationCosts.editProvince)
          : (this.editCost += this.informationCosts.setProvince);
    }
  }

  submitInfo() {
    if (!this.editCost) {
      const payload = {
        code: "CUSTOM_HTML_ALERT",
        text: "Submit after changes.",
        dismissText: "Okey"
      };
      this.presentAlert(payload);
      return;
    }

    // this.mobile = this.utils.fixNumbers(this.mobile)
    // if ((this.mobile.length && !this.utils.checkMobileFormat(this.mobile))) {
    //   this.presentAlert({ code: 79 })
    //   return
    // }

    this.SmartAudioProvider.play("buttonClick");

    // if (this.mobile != '' && this.mobile != this.player.mobile)
    //   this.sendSMS()
    // else
    this.editAccount();
  }

  sendSMS() {
    this.mobile = this.utils.fixNumbers(this.mobile);
    if (!this.utils.checkMobileFormat(this.mobile)) {
      this.presentAlert({ code: 79 });
      return;
    }

    this.loading = true;
    this.restore = false;

    const body = { mobile: this.mobile, restore: false };
    this.http.post("/users/sendSMS", body, (res) => {
      this.loading = false;
      if (res.status == "success")
        this.presentAlert(
          { code: 52, text: "Enter the verification code sent." },
          (data: any) => {
            if (data) {
              this.editAccount(data);
            }
          }
        );
      else this.presentAlert(res.data);
    });
  }

  editAccount(code?) {
    this.loading = true;
    let body = {
      name: this.username,
      // mobile: this.mobile,
      province: this.province
    };

    this.SmartAudioProvider.play("buttonClick");

    // if (code) { body['code'] = this.utils.fixNumbers(code) }

    this.http.post("/users/edit", body, async (res) => {
      this.loading = false;
      if (res.status == "success") {
        this.dataProvider.setPlayer({
          silver: res.data.silver,
          name: body.name,
          // mobile: body.mobile,
          province: body.province
        });

        this.editCost = 0;

        const toast = await this.toastCtrl.create({
          message: "Your Profile Updated.",
          duration: 3000,
          position: "middle",
          mode: "ios"
        });
        toast.present();
      } else this.presentAlert(res.data);
    });
  }

  startChat() {
    if (this.isForMe) return;

    this.SmartAudioProvider.play("buttonClick");
    this.loading = true;

    const body = { participantId: this.id };

    this.http.post("/chat/start", body, (res: any) => {
      this.loading = false;
      if (res.status === "success") {
        this.goToDirectChat(this.id);
      } else {
        this.presentAlert(res.data);
      }
    });
  }

  async goToClanPage() {
    // close profile
    await this.dismiss();

    // close any exist modal like leaderboard or history
    const otherModals = await this.modalCtrl.getTop();
    if (otherModals) {
      this.SmartAudioProvider.play("back");
      otherModals.dismiss();
    }

    this.navCtrl.setDirection("root");
    this.router.navigate(["tabs/clan"]);
  }

  async goToDirectChat(directId: number) {
    // close profile
    await this.dismiss();

    // close any exist modal like leaderboard or history
    const otherModals = await this.modalCtrl.getTop();
    if (otherModals) {
      this.SmartAudioProvider.play("back");
      otherModals.dismiss();
    }

    this.navCtrl.setDirection("root");
    this.router.navigate(["tabs/inbox"], { state: { directId } });
  }

  logout() {
    this.dataProvider.setAuth(null);
    this.navCtrl.setDirection("root");
    this.router.navigate(["splash"]);
    this.dismiss();
  }

  openKeyboard() {
    this.keybordIsOpen = true;
  }

  closeKeyboard() {
    this.keybordIsOpen = false;
  }

  async presentAlert(payload: any, callback?: Function) {
    if (!this.dataProvider.isShowingAlert.value) {
      let prizeModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      prizeModal.present();
      if (typeof callback == "function") {
        prizeModal.onDidDismiss().then(async (res) => callback(res.data));
      }
    }
  }

  async dismiss() {
    this.SmartAudioProvider.play("back");
    await this.modalCtrl.dismiss();
  }
}
