import { Component } from "@angular/core";
import { NavController, ModalController } from "@ionic/angular";

import { DataProvider } from "../../services/data";
import { SmartAudioProvider } from "../../services/smart-audio";

@Component({
  selector: "page-alerts",
  templateUrl: "alerts.html"
})
export class AlertsPage {
  payload;

  text;
  title;
  code;

  textError;

  buttons = [];

  constructor(
    public navCtrl: NavController,
    private modalCtrl: ModalController,
    private dataProv: DataProvider,
    private SmartAudioProvider: SmartAudioProvider
  ) {
    this.dataProv.setAlertPage(true);
  }

  ngOnInit() {
    this.code = this.payload.code;

    if (typeof this.code == "string") {
      this.slectErrorType(this.code);
    } else this.dismiss();
  }

  slectErrorType(code) {
    switch (code) {
      case "LOW_TROPHY":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "You have not reached this state yet!";
        break;

      case "CHAT_ADMIN_NOT_FOUND":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Admin not found. Try again later!";
        break;
      case "NAME_LENGTH_IS_NOT_VALID":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Username must be alphanumeric with 3-15 characters";
        break;
      case "ALREADY_REQUEST_PARTICIPATE":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Thea game already started.";
        break;
      case "FIRST_LEAVE_THEN_JOIN":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "First leave then join!";
        break;
      case "CONFIRM_LEAVE_CLAN":
        const minutes = this.dataProv.getConfig().scales.clan.EVENT_DURATION;
        this.buttons = [
          {
            text: "Cancel",
            class: "success",
            click: () => this.dismiss(false)
          },
          {
            text: "Yes",
            class: "aqua-2",
            click: () => this.dismiss(true)
          }
        ];
        this.title = "Leave team?";
        this.text = `You can start playing in a team ${minutes} mins after you joined!`;
        this.SmartAudioProvider.play("noMoney");
        break;
      case "SERVER_ERROR":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Server error! Try again later.";
        break;
      case "AT_LEAST_TWO_MEMBER_FOR_PARTICIPATE":
        const min = this.dataProv.getConfig().scales.clan
          .MINIMUM_MEMBER_FOR_PLAY;
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = `At least ${min} members are required to get started.`;
        break;
      case "COLLECT_YOUR_LIVE_PRIZE":
        this.buttons = [
          {
            text: "Collect",
            class: "success",
            click: () => {
              window.open(
                "mailto:harakaquiz@gmail.com?subject=Get%20the%20prize!&body=Hello%2C+I%E2%80%99m+%E2%80%9Cusername%E2%80%9D+in+Haraka+game.+I%E2%80%99m+sending+you+this+message+to+collect+my+prize.%0D%0A%0D%0ABest."
              );
              this.dismiss();
            }
          }
        ];
        this.title = "Congrats!";
        this.text = "Tap the button to collect your prize";
        break;
      case "NO_MORE_QUIZZES_LEFT":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "No more quizzes left!";
        break;
      case "NO_OPPONENT_FOUND":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "No opponent found!";
        break;
      case "CHAT_NOT_FOUND":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.SmartAudioProvider.play("noMoney");
        this.text = "Chat not found!";
        break;
      case "CANNOT_BLOCK_SYSTEM_USER":
        this.buttons = [
          {
            text: "Ooh sorry!",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.SmartAudioProvider.play("noMoney");
        this.text = "You can not blocka me :)";
        break;
      case "CLAN_REQUIRED_LEVEL_NOT_REACHED":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Clan required level not reached";
        break;
      case "CLAN_IS_PRIVATE ":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Clan is private, you need invitation to join!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "EVENT_FINISHED":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Event finished";
        break;
      case "YOU_ARE_NOT_IN_CLAN":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "You are not in clan!";
        break;
      case "FIRST_ROUND_TIMEOUT":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Times over!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "KICKED_NEED_INVITATION":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "You can not join to this Team without invitation!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "ALREADY_INVITED_TO_CLAN":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Already member!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "ALREADY_PLAYED":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "The game already played!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "MAYBE_KICKED":
        this.buttons = [
          {
            text: "Got it",
            class: "success",
            click: () => this.dismiss()
          }
        ];
        this.text = "Maybe you are kicked!";
        this.SmartAudioProvider.play("noMoney");
        break;
      case "CUSTOM_HTML_ALERT":
        if (this.payload.buttons) {
          this.buttons = this.payload.buttons;
        }

        if (this.payload.dismissText) {
          this.buttons.push({
            text: this.payload.dismissText,
            class: "aqua",
            click: () => this.dismiss()
          });
        }
        this.payload.title ? (this.title = this.payload.title) : "";
        this.text = this.payload.text;
        break;
      case 1000:
        this.buttons = [
          {
            text: "Try again",
            class: "success",
            click: () => this.retry()
          }
        ];
        this.text = this.text
          ? this.text
          : "Unable to connect with the server! Check your connection.";
        break;
      default:
        this.buttons = [
          {
            text: "Try again",
            class: "success",
            click: () => this.retry()
          }
        ];
        this.text = this.text ? this.text : "Connection error! Try again.";
        // this.code = 900
        break;
    }
  }

  retry() {
    this.dismiss(true);
    if (this.payload["retry"] != undefined) {
      this.payload.retry();
    }
  }

  inAppBrowser(URL: string) {
    window.open(URL);
  }

  clickOutside(event: any) {
    if (event.target.className.includes("alert-container ion-padding")) {
      this.dismiss(false);
    }
  }

  async alertInAlertPage(payload: any) {
    await this.dismiss();
    if (payload) {
      let alertModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      alertModal.present();
    }
  }

  async dismiss(data?: any) {
    if (data) {
      this.SmartAudioProvider.play("submit");
    } else {
      this.SmartAudioProvider.play("back");
    }

    await this.modalCtrl.dismiss(data);
    this.dataProv.setAlertPage(false);
  }

  ionViewDidLeave() {
    this.dataProv.setAlertPage(false);
  }
}
