import { Component } from "@angular/core";
import { NavController, ModalController } from "@ionic/angular";
import { HttpProvider } from "../../services/http";
import { DataProvider } from "../../services/data";
import { ClanService } from "../../services/clan";
import { SmartAudioProvider } from "../../services/smart-audio";
import { AbilityTranslator } from "../../../assets/Constants";
import { NavigateService } from "src/app/services/navigate";

@Component({
  selector: "page-leadership",
  templateUrl: "leadership.html"
})
export class LeadershipPage {
  data: any;
  keys;
  title = "Leaderboards";
  loading = false;

  player: any;
  clan: any;

  selected_categories = false;
  position = "root";

  title_list;
  leaderList = [
    {
      expectedRes: { leaderboard: [] },
      property: "brain",
      name: "Performance Beads",
      image: "brain-leaders",
      query: "BRAIN"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "xp",
      name: "Level",
      image: "xp-leaders",
      query: "xp"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "trophy",
      name: "Journey",
      image: "challenge-leaders",
      query: "trophy"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "wins",
      name: "Quiz",
      image: "quiz-leaders",
      query: "CITY"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "wins",
      name: "Win",
      image: "total-leaders",
      query: "wins"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "stars",
      name: "Team",
      image: "total-leaders",
      query: "clan-stars"
    },
    {
      expectedRes: { leaderboard: [] },
      property: "liveScore",
      name: "Live",
      image: "royal-leaders",
      query: "royalScore"
    }
  ];

  quiz = [];
  abilities = [
    {
      expectedRes: { leaderboard: [] },
      property: "brain",
      name: "Performance Beads",
      image: "brainScore",
      query: "brainScore"
    }
  ];

  leaders = {};
  leadership: any;
  index;
  property: any;
  bgImagePath = "../../assets/imgs/leadership/leaderboars-category.png";
  quizImagePath = "../../assets/imgs/icons/";

  constructor(
    public navCtrl: NavController,
    private modalCtrl: ModalController,
    private httpProvider: HttpProvider,
    private dataProvider: DataProvider,
    private clanService: ClanService,
    private Navigate: NavigateService,
    private smartAudioProvider: SmartAudioProvider
  ) {
    this.setListsItems();
    this.title_list = this.leaderList;

    this.player = this.dataProvider.getPlayer();
    this.clan = this.clanService.clan;
  }

  ionViewDidEnter() {
    this.smartAudioProvider.play("change");
  }

  setListsItems() {
    const games = this.dataProvider.getGames().slice(0, 6);
    games.map((city) => {
      this.quiz.push({
        expectedRes: { leaderboard: [] },
        property: "wins",
        name: city.name,
        image: city.info,
        query: "usercity-" + city.id
      });
    });

    Object.keys(AbilityTranslator).map((ability) => {
      this.abilities.push({
        expectedRes: { leaderboard: [] },
        property: "brain",
        name: AbilityTranslator[ability],
        image: ability,
        query: ability
      });
    });
  }

  async goToProfile(id: number, isForMe: boolean) {
    this.smartAudioProvider.play("buttonClick");

    if (this.property === "stars") {
      this.Navigate.clanProfile({ id });
    } else {
      this.Navigate.profile({ id, isForMe });
    }
  }

  selectCategories(item) {
    this.smartAudioProvider.play("buttonClick");

    this.title = item.name;

    if (item.query === "BRAIN") {
      this.title_list = this.abilities;
      this.position = "brainList";
      return;
    } else if (item.query === "CITY") {
      this.title_list = this.quiz;
      this.position = "quizList";
      return;
    }

    this.position = item.query;
    this.loading = true;
    this.selected_categories = true;
    this.property = item.property;

    const body = { category: item.query, clanId: this.player.clanId };
    this.httpProvider.post(
      "/users/leaderboard",
      body,
      (res) => {
        this.loading = false;
        if (res.status == "success") {
          this.data = res.data;
          if (item.query === "clan-stars") {
            this.clan["rank"] = this.data.rank || 0;
            this.clan["count"] = this.data[item.property] || 0;
          } else {
            this.player["rank"] = this.data.rank || 0;
            this.player["count"] = this.data[item.property] || 0;
          }

          for (let key in item.expectedRes) {
            this.leaders[key] = res.data[key];
            let list = this.leaders[key];

            this.leaders[key].map((element, index) => {
              if (index == 0) element.rank = 1;
              else if (
                element[this.property] === list[index - 1][this.property]
              )
                element.rank = this.leaders[key][index - 1].rank;
              else element.rank = index + 1;
            });
          }

          this.keys = Object.keys(item.expectedRes);
          this.leadership = this.leaders["leaderboard"];
          this.index = 0;
        }
      },
      () => {
        this.dismiss();
        this.loading = false;
      }
    );
  }

  listSelect(item, index) {
    this.leadership = this.leaders[item];
    this.index = index;

    if (item === "clansLeadership") {
      this.player["rank"] = this.data.dayRank || 0;
      this.player["count"] = this.data.dayWins || 0;
    } else {
      this.player["rank"] = this.data.rank || 0;
      this.player["count"] = this.data.wins || 0;
    }
  }

  dismiss() {
    this.smartAudioProvider.play("back");
    this.selected_categories = false;
    this.loading = false;

    if (this.position.includes("usercity-")) {
      this.title = "The best in terms of quizzes";
      this.position = "quizList";
    } else if (this.property == "brain") {
      this.property = "";
      this.title = "The best performance beads";
      this.position = "brainList";
    } else if (this.position != "root") {
      this.title_list = this.leaderList;
      this.title = "Leaderboards";
      this.position = "root";
    } else {
      this.modalCtrl.dismiss();
    }
  }
}
